import React, { FormEvent } from 'react';
import Engine from '../engine/Engine';
import AppContext from '../context/context';
import { Datamap } from '../engine/Datamap';
import BottomBar from './BottomBar';
import CommandButtonFC from './comps/CommandButtonFC';
import ProphetUI from './ProphetUI';
import './App.scss'
import NavBar from './comps/NavBar';
import ResetUI from './comps/ResetUI';
import FollowersUI from './FollowersUI';
import { msToFullHMS, compressString } from '../engine/util';
import AdornmentUI from './comps/AdornmentUI';
import OfflineOverlay from './comps/OfflineOverlay';
import RocketUI from './comps/RocketUI';
import RelicUI from './comps/RelicUI';
import TipFC from './comps/TipFC';
import PantheonUI from './comps/PantheonUI';


export default class App extends React.Component<{}, { data: Datamap }> {

    engine: Engine;

    constructor(props: Readonly<{}>) {
        super(props);
        this.engine = new Engine(this.notify)
        //this.engine.quietLoad();
        this.state = {
            data: this.engine.datamap
        }
        //this.engine.quietLoad();
        window.addEventListener("keypress", this.handleKey);
    }

    componentWillUnmount() {
        window.removeEventListener('keypress', this.handleKey);
    }

    handleKey = (e: KeyboardEvent) => {
        if (this.engine.datamap.nacData.timeSet != 0) this.engine.executeCommand(`keypress ` + e.key)
    }

    notify = () => {
        this.setState({ data: this.engine.datamap })
    }

    submitCommand = (command: string) => {
        this.engine.executeCommand(command)
    }

    setname = (ev: FormEvent<HTMLFormElement>) => {
        let target = ev.target as HTMLFormElement;
        let children = target.children;
        let textInput = children[0] as HTMLInputElement;
        console.log(textInput.value);
        this.submitCommand(`nac setname ${textInput.value}`)

        ev.preventDefault();
    }

    render() {
        return (
            <div className='App' style={{ width: `100%`, height: `100%` }}>
                <AppContext.Provider value={{
                    data: this.state.data,
                    submitCommand: this.submitCommand
                }}>
                    <div style={{ display: 'flex', flexDirection: 'column', height: '100%' }}>

                        <div className='TopBar'>
                            <CommandButtonFC command='save' name='Save' />

                            {(this.state.data.nacData.name === '') && <form onSubmit={this.setname}>
                                <input type='text' defaultValue='noname' />
                                <button className='DCommandFC' type='submit'>Set Name</button>
                            </form>}

                            {(this.state.data.nacData.name !== '') && <div onClick={() => { this.submitCommand(`nac clearname`) }}>
                                {`The Prophet, ${this.state.data.nacData.name}!`}<TipFC tip={<div>Click to rename!</div>} />
                            </div>}

                            <div>
                                {msToFullHMS(this.state.data.gameTime.currentRun.time)}
                            </div>

                            <CommandButtonFC command='load' name='Load' />
                            <CommandButtonFC command='export' name='Export' />
                            <div>
                                <button className='DCommandFC' onClick={() => { (document.getElementById('file-input') as HTMLInputElement).click() }} >
                                    Import
                            </button>
                                <input id="file-input" type="file" name="name" style={{ display: "none" }}
                                    onChange={(e) => {
                                        let list = e.target.files;
                                        if (list) {
                                            let file = list[0];
                                            var reader = new FileReader();
                                            
                                            //reader.readAsArrayBuffer(file)
                                            reader.readAsText(file, 'UTF-8');

                                            // here we tell the reader what to do when it's done reading...
                                            reader.onload = readerEvent => {
                                                let target = readerEvent.target;
                                                if (target) {
                                                    let content = target.result;
                                                    console.log(content);
                                                    this.submitCommand(`import ${content}`)

                                                }
                                            }

                                        }

                                    }} />
                            </div>
                        </div>

                        <ProphetUI />

                        <NavBar />

                        <div className='SelectedArea'>
                            {MAINNAVS[this.state.data.nav.mainArea]}
                        </div>

                        <BottomBar />
                        <OfflineOverlay />
                    </div>
                </AppContext.Provider>
            </div>
        )
    }
}



const MAINNAVS = [<RelicUI />, <ResetUI />, <FollowersUI />, <AdornmentUI />, <RocketUI />, <PantheonUI />]