import ExpoCurve, { expoI_getCost, expoI_maxBuyAmount, expoI_buyNCost } from "./ExpoCurve";

import { DecoupledCommand2 } from "./ICommand";

import { Datamap } from "../Datamap";
import { ReactNode } from "react";

export default interface ExpoCommand {
    expo: ExpoCurve,
    countFns: CountFns;
    currencyFns: CurrencyFns;
    
    //command: DecoupledCommand2,
    name: string;
    command: string;
    currencyName?: string;

    hidden: (data: Datamap) => boolean;

    change?: (data: Datamap) => string;
    img?: string;
    tip?: ReactNode;
}

export interface CountFns {
    get: (data: Datamap) => number;
    gain: (data: Datamap, ammount: number) => void;
}

export interface CurrencyFns {
    get: (data: Datamap) => number;
    spend: (data: Datamap, ammount: number) => void;
}

export const CurrencyFn_USD: CurrencyFns = {
    get: (data: Datamap) => data.prophetData.congregation.USD,
    spend: (data: Datamap, ammount: number) => { data.prophetData.congregation.USD -= ammount }
}


export function expoCommand_buy(data: Datamap, expoCommand: ExpoCommand) {
    if (expoCommand_Able(data, expoCommand) === false) { return; }
    else {
        expoCommand.currencyFns.spend(data, expoCommand_Cost(data, expoCommand));
        expoCommand.countFns.gain(data, 1);
    }
}

export function expoCommand_Cost(data: Datamap, expoCommand: ExpoCommand) {
    return expoI_getCost(expoCommand.expo, expoCommand.countFns.get(data))
}

export function expoCommand_Able(data: Datamap, ep: ExpoCommand) {
    return expoCommand_Cost(data, ep) <= ep.currencyFns.get(data);
}

export function expoCommand_maxAmmount(data: Datamap, ep: ExpoCommand) {
    return expoI_maxBuyAmount(ep.expo, ep.countFns.get(data), ep.currencyFns.get(data));
}
export function expoCommand_maxCost(data: Datamap, ep: ExpoCommand) {
    let ammount = expoCommand_maxAmmount(data, ep);
    return expoI_buyNCost(ep.expo, ep.countFns.get(data), ammount);
}

export function expoCommand_buyMax(data: Datamap, ep: ExpoCommand) {
    let ammount = expoCommand_maxAmmount(data,ep);
    let cost = expoCommand_maxCost(data,ep)

        if (ep.currencyFns.get(data) >= cost) {
            ep.currencyFns.spend(data, cost );
            ep.countFns.gain(data, ammount);
        }
}

