import Engine from "../Engine";
import { DecoupledCommand } from "../interfaces/ICommand";
import { getRandomInt, WORLD_POP } from "../util";
import { Datamap } from "../Datamap";
import { piety_purchase_buy_congregation, piety_purchase_buy_reincarnation, piety_purchase_buy_holypower, holiness_holyPowerUpgrade_do, holiness_holyPumpUpgrade_do, holiness_maxHolyPowerUpgrade_do, holiness_believerRetention_do, holiness_exRelations_do, holiness_buildingMulti_do, holinessUpgradesInit, UnlockMiracles, holiness_devotionEffectiveness_do } from "./PietyPurchases";
import { devotion_mult, DP_Upgrades, DPUpgrades_Init } from "./Followers";
import { relicUSDupgrade_cost } from "./USDRelicPurchases";
import { DevotionUpgradeData, DevotionUpgradeData_init } from "./DevotionPurchases";
import ITEM_POWERS from "./ItemPowers";
import { PantheonData_init } from "./Pantheon/Types";
import { expoCommand_buy, expoCommand_buyMax } from "../interfaces/ExpoCommand";
import { USD_UpgradeTithe, USD_Upgrade_Pamphlets } from "./USDPurchases";

export default class ProphetModule {

    constructor(public engine: Engine) {

    }

    get data() {
        return this.engine.datamap.prophetData;
    }

    processCommands(cmdArray: string[]) {
        switch (cmdArray[1]) {
            case 'pray':
                this.pray();
                break;

            case 'proselytize':
                this.proselytize();
                break;

            case 'preach':
                this.preach();
                break;

            case 'relicupgrade':
                this.upgradeRelic(cmdArray[2])
                break;


            case 'buyrelic':
                this.buyRelic(cmdArray[2]);
                break;

            case 'buymaxrelic':
                this.buyMaxRelic(cmdArray[2]);
                break;

            case 'buyto25relic':
                this.buyToNRelic(cmdArray[2], 25);
                break;

            case 'setbuyrelic':
                switch (cmdArray[2]) {
                    case '1':
                        this.data.relicsetbuy = 1;
                        break;

                    case '25':
                        this.data.relicsetbuy = 25;
                        break;

                    case 'max':
                        this.data.relicsetbuy = 99;

                        break;

                    default:
                        break;
                }
                break;

            case 'prestige':
                this.prestige();
                break;

            case 'devotedprestige':
                this.devotedprestige();
                break;

            case 'congregate':
                piety_purchase_buy_congregation(this.engine.datamap)
                break;

            case 'reincarnate':
                piety_purchase_buy_reincarnation(this.engine.datamap)
                break;

            case 'holypower':
                piety_purchase_buy_holypower(this.engine.datamap)
                break;

            case 'holypowerUpgrade':
                holiness_holyPowerUpgrade_do(this.engine.datamap);
                break;

            case 'holypumpupgrade':
                holiness_holyPumpUpgrade_do(this.engine.datamap);
                break;

            case 'maxholypowerupgrade':
                holiness_maxHolyPowerUpgrade_do(this.engine.datamap);
                break;

            case 'believerretentionupgrade':
                holiness_believerRetention_do(this.engine.datamap);
                break;

            case 'exrelationsupgrade':
                holiness_exRelations_do(this.engine.datamap);
                break;

            case 'bmupgrade':
                holiness_buildingMulti_do(this.engine.datamap);
                break;

            case 'de_upgrade':
                holiness_devotionEffectiveness_do(this.engine.datamap);
                break;

            case 'unlockmiracles':
                this.engine.datamap.rocketData.unlocked = true;
                if (UnlockMiracles.cost) this.engine.datamap.prophetData.piety -= UnlockMiracles.cost(this.engine.datamap);
                break;

            case 'usdupgrade':
                switch (cmdArray[2]) {
                    case 'tithe':
                        expoCommand_buy(this.engine.datamap, USD_UpgradeTithe)
                        break;

                    case 'tithemax':
                        expoCommand_buyMax(this.engine.datamap, USD_UpgradeTithe);
                        break;

                    case 'pamphlets':
                        expoCommand_buy(this.engine.datamap, USD_Upgrade_Pamphlets)
                        break;

                    case 'pamphletsmax':
                        expoCommand_buyMax(this.engine.datamap, USD_Upgrade_Pamphlets);
                        break;
                
                    default:
                        console.log('broken in prophet->usdupgrade');
                        
                        break;
                }
                break

            default:
                console.log('broke in prophet comand');

                break;
        }
    }

    upgradeRelic(arg0: string) {
        let upgradeIndex = parseInt(arg0);
        let cost = relicUSDupgrade_cost(this.engine.datamap, upgradeIndex);

        if (this.data.congregation.USD >= cost) {
            this.data.congregation.USD -= cost;
            this.data.relicUSDUpgrades[upgradeIndex]++;
        }
    }


    buyRelic(arg0: string) {
        let relicIndex = parseInt(arg0);
        let cost = relic_getCost(relicIndex, this.engine.datamap);

        if (this.data.piety >= cost) {
            this.data.piety -= cost;
            this.data.relics[relicIndex]++;
        }
    }

    buyMaxRelic(arg0: string) {
        let relicIndex = parseInt(arg0);
        let ammount = relic_maxBuyAmount(relicIndex, this.engine.datamap)
        let cost = relic_maxBuyCost(relicIndex, this.engine.datamap);

        if (this.data.piety >= cost) {
            this.data.piety -= cost;
            this.data.relics[relicIndex] += ammount;;
        }
    }

    buyNRelic(arg0: string, n: number) {
        let relicIndex = parseInt(arg0);
        let cost = relic_buyNCost(relicIndex, this.engine.datamap, n)

        if (this.data.piety >= cost) {
            this.data.piety -= cost;
            this.data.relics[relicIndex] += n;
        }
    }

    buyToNRelic(arg0: string, n: number) {
        let relicIndex = parseInt(arg0);
        let remainder = this.data.relics[relicIndex] % n;
        let needToN = n - remainder;

        let cost = relic_buyNCost(relicIndex, this.engine.datamap, needToN)

        if (this.data.piety >= cost) {
            this.data.piety -= cost;
            this.data.relics[relicIndex] += needToN;
        }
    }

    dayTrigger(days: number) {

        

        if (this.data.holyPower > 0) this.holyDump(days);

        if (this.data.congregation.doorknocker > 0) this.doorknockers(days);
        this.engine.followers.dayTriggerParitioners(days);
        if (this.data.believers > 0) this.believersLeave(days);


        if (this.data.congregation.preachers > 0) this.preachers(days);
        if (this.data.congregation.devotee > 0) this.devotees(days);

        if (this.engine.datamap.automation.auto_buyToNext && this.data.congregation.unlocked) {
            this.engine.followers.recruit_doorknocker_max();
            this.engine.followers.recruit_devotee_max();
            this.engine.followers.recruit_preacher_max();
        }
        
    }

    devotees(days: number) {
        this.data.devotion += devotee_devotion_gain(this.engine.datamap) * days
    }

    doorknockers(days: number) {
        this.data.believers += (doorknocker_beliver_gain(this.engine.datamap)) * days
    }

    believersLeave = (days: number) => {
        for (let i = 0; i < days; i++) {

            let loss = getRandomInt(0, helper_prophet_maxBelieversDecay(this.engine.datamap));
            let boundLoss = Math.min(this.data.believers, loss)
            this.data.ex_believers += boundLoss;
            this.data.believers -= boundLoss;

        }
    }

    holyDump = (days: number) => {
        if (this.data.devotionUpgrades.def.b) return;
        this.data.holyPower = Math.max(this.data.holyPower - (24 * (1 + this.engine.datamap.followers.DPUpgrades.heavyHolyPower)), 0);
    }

    preachers(days: number) {
        let gain = getRandomInt(0, preachers_max_deovtion_gain(this.engine.datamap));

        this.data.devotion += gain * days;
    }

    preach() {
        this.data.devotion += getRandomInt(0, preach_max_deovtion_gain(this.engine.datamap));
    }

    proselytize() {
        if (this.engine.datamap.prophetData.holiness === 0) return;
        this.engine.datamap.prophetData.holiness--;
        let gain = getRandomInt(0, helper_prophet_maxBeliverGainNet(this.engine.datamap));
        this.data.believers += gain;
    }

    pray() {
        this.data.piety++;
        if (this.data.holyPowerUnlocked) {
            this.holyPump();
        }
        if (this.data.devotionUpgrades.abc.b) {
            let delta = 1000;
            delta += delta * this.engine.datamap.pantheon.production.c
            this.processDetla(delta);
        }
        ITEM_POWERS.prayer_upgrades(this.engine.datamap);
        
    }

    holyPump = () => {
        this.data.holyPower = Math.min(this.data.holyPower + 1 * Math.pow(2, this.engine.datamap.holinessUpgrades.holyPumpUpgrade) * Math.pow(10, this.engine.datamap.followers.DPUpgrades.superHolyPump), prophet_maxHolyPower(this.engine.datamap));
    }

    processDetla(delta: number) {

        delta = delta * relicDeltaMult(this.engine.datamap)

        RELICS.forEach((relicData, relicIndex) => {
            if (this.data.relics[relicIndex] === 0) return;
            this.data.relicTimers[relicIndex] += delta;
            let timeCost = relic_getTime(relicIndex);

            if (this.data.relicTimers[relicIndex] >= timeCost) {
                let count = Math.floor(this.data.relicTimers[relicIndex] / timeCost)
                this.data.relicTimers[relicIndex] -= timeCost * count;
                this.data.piety += relic_getProduction(relicIndex, this.engine.datamap) * count
            }
        });

        if (this.data.piety > this.data.maxPiety) {
            this.data.maxPiety = this.data.piety;
        }

    }

    pietyDecay(delta: number) {
        //lookup math to simulate this over time
        let decay = (delta / 1000) * (helper_prophet_pietyDecay(this.engine.datamap));
        decay = Math.floor(decay);
        return decay;
    }

    prestige = () => {
        
        let ppgain = prophet_prestige_gain(this.engine.datamap);
        if (ppgain === 0 || this.data.prestigeUnlocked === false) return;
        
        let oldData = this.data;
        
        let itemDevotion = 0;
        if (this.engine.datamap.adornments.equiped.robe) {
            if (this.engine.datamap.adornments.equiped.robe.origin === 'ruins') {
                itemDevotion = Math.floor(this.engine.datamap.adornments.equiped.robe.level * .01 * oldData.devotion);
            }
        }

        let oldMult = oldData.holiness;
        let newCount = this.data.save.reincarnationCount + 1;
        let selected = this.data.relicsetbuy;
        let newOld = {
            preachers: this.data.congregation.preachers,
            preacherLevel: this.data.congregation.preacherLevel,
            devotee: this.data.congregation.devotee,
            devoteeLevel: this.data.congregation.devoteeLevel,
            doorknocker: this.data.congregation.doorknocker,
            doorknockerLevel: this.data.congregation.doorknockerLevel,
        }

        this.engine.datamap.prophetData = init_ProphetData();

        this.data.holiness = oldMult + Math.floor(ppgain);
        if (this.data.holiness < 0) this.data.holiness = 0;
        this.data.startingHoliness = this.data.holiness
        this.data.relicsetbuy = selected;
        this.data.save.reincarnationCount = newCount;
        this.data.congregation.old = newOld;
        this.data.devotion += itemDevotion

        this.prestigeCleanup();
    }

    devotedprestige = () => {
        let newCount = this.data.save.reincarnationCount + 1;


        this.engine.datamap.prophetData = init_ProphetData();

        this.engine.datamap.followers.devotedPrestige++;
        this.data.save.reincarnationCount = newCount;
        
        this.prestigeCleanup();
        this.hardCleanup();
    }
    
    hardCleanup = () => {
        this.engine.datamap.pantheon = PantheonData_init();
        this.engine.datamap.holinessUpgrades = holinessUpgradesInit()
    }

    prestigeCleanup = () => {
        this.engine.datamap.gameTime.currentRun.time = 0;
        this.engine.datamap.nav.mainArea = 0
        this.engine.gameTime.reset();
        this.engine.rocket.reset();
        this.engine.adornments.exploring.reset();
        if (this.engine.datamap.followers.DPUpgrades.reset) {
            this.engine.datamap.followers.DPUpgrades = DPUpgrades_Init();
        }
    }
}

export function relicDeltaMult(data:Datamap){
    let mult = 1;
    
    mult += (.1 * data.followers.DPUpgrades.fasterRelicProduction)
    mult += (.1 * data.pantheon.time.a);

    return mult;
}

export function availablePeople (data:Datamap) {
    let congregation = data.prophetData.congregation
    return WORLD_POP - data.prophetData.believers - data.prophetData.ex_believers - congregation.devotee - congregation.doorknocker - congregation.preachers
}

export function preach_max_deovtion_gain(data: Datamap) {
    return data.prophetData.believers;
}

export function devotee_devotion_gain(data: Datamap) {
    let count = data.prophetData.congregation.devotee
    let power = data.prophetData.congregation.devoteeLevel + 1;
    count = count * ITEM_POWERS.effective_devotee_mult(data);
    power = power * ITEM_POWERS.devotee_power_mult(data);    
    return count * power
}

export function doorknocker_beliver_gain(data: Datamap) {
    let gain = data.prophetData.congregation.doorknocker * (1 + data.prophetData.congregation.doorknockerLevel)
    gain += gain * data.followers.DPUpgrades.dailyBelieverGain;
    if (data.prophetData.devotionUpgrades.def.a) gain = gain * 2
    let harm = helper_prophet_exBeliever_harm(data);
    let realPossibleGain = Math.max(0, gain - harm);
    let realGain = Math.min(availablePeople(data),realPossibleGain)
    return realGain;

}

export function preachers_max_deovtion_gain(data: Datamap) {
    return preach_max_deovtion_gain(data) * data.prophetData.congregation.preachers * (1 + data.prophetData.congregation.preacherLevel);
}

export function helper_prophet_maxBelieversDecay(data: Datamap) {
    let total_power = helper_prophet_size_believerDecay(data) + helper_prophet_exBeliever_believerDecay(data);

    return Math.floor(total_power)

}


export function helper_prophet_size_believerDecay(data: Datamap) {
    let size = data.prophetData.believers;
    let power = size / 10;
    let total_power = power / (1 + data.holinessUpgrades.believerRetention);
    return Math.floor(total_power);
}

export function helper_prophet_maxBeliverGain(data: Datamap) {
    return Math.floor(Math.log(1 + data.prophetData.piety))
}

export function helper_prophet_exBeliever_harm(data: Datamap) {
    let ex = data.prophetData.ex_believers;
    let power = ex / 10;
    let total_power = (power) / (1 + data.holinessUpgrades.exRelations);
    return Math.floor(total_power);
}

export function helper_prophet_exBeliever_believerDecay(data: Datamap) {
    let ex = data.prophetData.ex_believers;
    let power = ex / 10;
    let total_power = (power) / (1 + data.holinessUpgrades.exRelations);
    total_power = total_power / (1 + data.holinessUpgrades.believerRetention);
    return Math.floor(total_power);
}

export function helper_prophet_maxBeliverGainNet(data: Datamap) {
    let gain = helper_prophet_maxBeliverGain(data);
    let harm = helper_prophet_exBeliever_harm(data);
    return Math.max(0, gain - harm)
}

export function helper_prophet_pietyDecay(data: Datamap) {
    return Math.floor(data.prophetData.piety / 10000) * data.gameTime.year;
}

export function prophet_maxHolyPower(data: Datamap) {
    let relics = data.prophetData.relics;
    let sum = relics[0] + relics[1] + relics[2] + relics[3] + relics[4];
    sum = sum * (1 + data.holinessUpgrades.maxHolyPowerUpgrade)
    sum += sum * data.pantheon.production.a * 10
    return sum;
}

