export const RELICS = [LemonadeStand, NewspaperDelivery, CarWash, PizzaDelivery, DonutShop,
    FakeNews1, FakeNews2, FakeNews3, FakeNews4, FakeNews5]

export function relic_getCost(relicIndex: number, data: Datamap) {
    let relicData = RELICS[relicIndex];

    let owned = data.prophetData.relics[relicIndex];
    let cost_base = relicData.initialCost;
    let coefficient = relicData.coefficient;

    return Math.floor(cost_base * Math.pow(coefficient, owned))
}

export function relic_canBuy(relicIndex: number, data: Datamap) {
    let cost = relic_getCost(relicIndex, data);
    return data.prophetData.piety >= cost;
}

export function relic_getTime(relicIndex: number) {
    return RELICS[relicIndex].initialTime * 1000;
}

export function relic_getProduction(relicIndex: number, data: Datamap) {
    let val = Math.floor(
        data.prophetData.relics[relicIndex] *
        RELICS[relicIndex].initialRevenue *
        relic_getProductionMulti(relicIndex, data) *
        holiness_mult(data) *
        relic_hoylPowerMulti(data) *
        devotion_mult(data) *
        reincation_mult(data) *
        special_relic_mult(relicIndex, data) *
        item_relic_mult(relicIndex, data)
    )
    return val
}

export function item_relic_mult(index:number, data :Datamap) {
    let mult = 1;
    let eq = data.adornments.equiped;
    if (eq.cap) {
        if (eq.cap.origin === 'ruins') {
            mult += eq.cap.level;
        }
    }
    return mult;
}

export function special_relic_mult(index: number, data: Datamap) {
    let mult = 1;
    mult += time_relic_mult(data)
    if (data.prophetData.devotionUpgrades.abc.c) mult = mult * (index + 1);
    if (data.prophetData.devotionUpgrades.def.c) mult = mult * (index + 1);
    return mult;
}

export function time_relic_mult(data: Datamap) {
    return year_relic_mult(data) + month_relic_mult(data)
}

export function year_relic_mult(data: Datamap) {
    return data.followers.DPUpgrades.yearMultiplier * data.gameTime.year
}

export function month_relic_mult(data: Datamap) {
    return data.followers.DPUpgrades.monthMultiplier * data.gameTime.month
}

export function holiness_mult(data: Datamap) {
    return CALC_holy_mult(data.prophetData.startingHoliness)
}

export function reincation_mult(data: Datamap) {
    return (data.prophetData.save.reincarnationCount + 1) * reincation_mult_from_superPrestiges(data)
}

export function reincation_mult_from_superPrestiges (data:Datamap) {
    return data.followers.devotedPrestige + 1
}

function CALC_holy_mult(holiness: number) {
    //return Math.log(holiness + 1)
    
    const CUTOFF = 10000
    let base = Math.sqrt(holiness) + 1
    if (base > CUTOFF) {
        let rest = Math.sqrt(Math.sqrt(holiness - CUTOFF * CUTOFF))
        return rest + CUTOFF
    }
    return base
}

export function next_holiness_mult(data: Datamap) {
    return CALC_holy_mult(data.prophetData.holiness + prophet_prestige_gain(data))
}

export function relic_getProductionPerecond(relicIndex: number, data: Datamap) {
    return Math.floor(relic_getProduction(relicIndex, data) / RELICS[relicIndex].initialTime * relicDeltaMult(data))
}

export function relic_hoylPowerMulti(data: Datamap) {
    let HPPower = Math.log(data.prophetData.holyPower + 1);
    let mult1 = data.holinessUpgrades.holypowerUpgrade + 1;
    let mult2 = data.followers.DPUpgrades.heavyHolyPower + 1;
    let mult3 = data.pantheon.production.b + 1;
    let val = HPPower * mult1 * mult2 * mult3;
    val = Math.floor(val);
    return val + 1
}

export const PRODMULTIPER25 = 1.5

export function Building_Multi(data: Datamap, index: number) {
    return 1.5 + (.1 * (data.holinessUpgrades.buildingMulti + data.prophetData.relicUSDUpgrades[index]))
}

export function relic_getProductionMulti(relicIndex: number, data: Datamap) {
    return Math.pow(Building_Multi(data, relicIndex), Math.floor(data.prophetData.relics[relicIndex] / 25))
}

export function relic_maxBuyAmount(relicIndex: number, data: Datamap) {
    let b = RELICS[relicIndex].initialCost;
    let r = RELICS[relicIndex].coefficient;
    let k = data.prophetData.relics[relicIndex];
    let c = data.prophetData.piety;

    let logresult = Math.log(1 + ((c * (r - 1)) / (b * Math.pow(r, k))))
    let logchange = logresult / Math.log(r);
    let result = Math.floor(logchange)

    return result;
}

export function prophet_prestige_gain(data: Datamap) {
    return Math.floor(Math.sqrt(Math.sqrt(data.prophetData.maxPiety) / 100))
}

export function relic_maxBuyCost(relicIndex: number, data: Datamap) {

    let n = relic_maxBuyAmount(relicIndex, data);

    return relic_buyNCost(relicIndex, data, n)
}

export function relic_buyNCost(relicIndex: number, data: Datamap, n: number) {
    let b = RELICS[relicIndex].initialCost;
    let r = RELICS[relicIndex].coefficient;
    let k = data.prophetData.relics[relicIndex];

    return Math.floor(b * (Math.pow(r, k) * (Math.pow(r, n) - 1)) / (r - 1))
}

export function relic_buyNextCost(relicIndex: number, data: Datamap, next: number) {
    let k = data.prophetData.relics[relicIndex];
    let n = next - k % next;

    return relic_buyNCost(relicIndex, data, n)
}

export function relic_canBuyN(relicIndex: number, data: Datamap, n: number) {
    let cost = relic_buyNCost(relicIndex, data, n);
    return data.prophetData.piety >= cost
}

export function relic_canBuyNext(relicIndex: number, data: Datamap, next: number) {
    let k = data.prophetData.relics[relicIndex];
    let n = next - k % next;

    return relic_canBuyN(relicIndex, data, n);
}

export const CMD_prophet_pray: DecoupledCommand = {
    name: "Pray",
    command: 'prophet pray',
}

export const CMD_prophet_proselytize: DecoupledCommand = {
    name: "Proselytize",
    command: 'prophet proselytize',
}

export const CMD_prophet_preach: DecoupledCommand = {
    name: "Preach",
    command: 'prophet preach',
}

function can_devotedPrestige(data: Datamap) {
    return data.prophetData.devotion >= devotedPrestige_cost(data);
}

export function devotedPrestige_cost(data: Datamap) {
    return 1e8 * Math.pow(69.69, data.followers.devotedPrestige);
}

function hide_devotedPrestige(data: Datamap) {
    return (data.prophetData.congregation.paritioners[1] === 0) && (data.followers.devotedPrestige === 0)
}

export const CMD_prophet_devotedPrestige: DecoupledCommand = {
    name: "Devoted Reincarnation",
    command: 'prophet devotedprestige',
    able: can_devotedPrestige,
    hidden: hide_devotedPrestige,
}