export function getRandomInt(min: number, max: number) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
}   

export function formatNumber(num:number): string {
    if (Math.abs(num) < 1e6) return num.toString();
    else return num.toExponential(2);
}
export function percentOf (num: number, base: number) {
    if (num === 0 || base === 0) return 'IDK';
    else return Math.floor(100*num/base).toString() + '%';
}

export function msToFullHMS (msTime: number) {
    
    let hour = Math.floor(msTime/HOUR_MS).toString();

    let minute = Math.floor((msTime%HOUR_MS)/HOUR_MS * 60).toString();
    if (minute.length === 1) minute = '0' + minute;

    let second = Math.floor((msTime%MINUTE_MS)/MINUTE_MS * 60).toString();
    if (second.length === 1) second = '0' + second;
    
    
    return `${hour}:${minute}:${second}`
}

export const SECOND_MS = 1000
export const MINUTE_MS = SECOND_MS * 60
export const HOUR_MS = MINUTE_MS * 60

export const WORLD_POP = 7.7e9


/**
 * @param {character[]} chars
 * @return {number}
 */
export var compressString = function(chars: string[]) {
    if (chars.length === 0) return 0
    var currentLetter = '';
    var currentCount = 0;
    var charsIndex = 0;
    for (var i = 0; i < chars.length; i++) {
        
        if (currentCount = 0) {
            currentLetter = chars[i]
            charsIndex++
        } else {
            
            if (chars[i] === currentLetter) {
                currentCount ++;
            } else {
                if (currentCount > 1) {
                    chars[charsIndex] = currentCount.toString();
                    charsIndex++
                } 
                currentCount = 1
                currentLetter = chars[i]
                charsIndex++
                
            }
        
        }
            
    }
    return charsIndex + 1
};

/**
 * @param {character[]} chars
 * @return {number}
 */
var compress = function(chars: string[]) {
    if (chars.length === 0) return 0
    var currentLetter = chars[0];
    var currentCount = 1;
    var charsIndex = 0;
    for (var i = 1; i < chars.length; i++) {
        
        if (chars[i] === currentLetter) {
            currentCount ++;
        } else {
            
            chars[charsIndex] = currentLetter
            charsIndex++
            
            
            if (currentCount > 1) {
                var intStr = currentCount.toString();
                for (var j = 0; j < intStr.length; j++) {
                    chars[charsIndex] = intStr[i];
                    charsIndex++                
                }
            } 
            currentCount = 1
            currentLetter = chars[i]
        }
            
    }
    
    chars[charsIndex] = currentLetter
    charsIndex++        
            
    if (currentCount > 1) {
        chars[charsIndex] = currentCount.toString();
        charsIndex++
    } 
    
    return charsIndex
};

interface LNode {
    next: LNode | null;
    val: number;
}
function ListNode(val: number): LNode {
    return {
        val, next: null
    }
}
/**
 * Definition for singly-linked list.
 * function ListNode(val) {
 *     this.val = val;
 *     this.next = null;
 * }
 */
/**
 * @param {ListNode} l1
 * @param {ListNode} l2
 * @return {ListNode}
 */
var addTwoNumbers = function(l1: LNode | null, l2: LNode | null) {
    var s1 = "";
    var s2 = "";
    
    while(l1 !== null) {
        s1 = s1 + l1.val.toString();
        l1 = l1.next
    }
    
    while(l2 !== null) {
        s2 = s2 + l2.val.toString();
        l2 = l2.next
    }
    
    s1 = s1.split("").reverse().join()
    s2 = s2.split("").reverse().join()
    
    var newVal = parseInt(s1) + parseInt(s2);
    var newS = newVal.toString();
    var newSSplit = newS.split("");
    
    var popped = newSSplit.pop();
        
    var currentNode = null;
    while (popped !== undefined) {
        var newNode = ListNode(parseInt(popped))
        newNode.next = currentNode;
        currentNode = newNode;
        popped = newSSplit.pop()
    }
    
    return currentNode
    
    
    
    
};