import { DecoupledCommand2 } from "../interfaces/ICommand";
import AbstractModule from "./AbstractModule";
import { SECOND_MS } from "../util";
import { Datamap } from "../Datamap";
import Engine from "../Engine";
import { SimData, RocketDataInit } from "./RocketTypes";

export default class Rocket extends AbstractModule {

    constructor(engine: Engine) {
        super(engine);
        this.updateEstimates();
    }

    get data() {
        return this.engine.datamap.rocketData;
    }

    reset () {
        this.engine.datamap.rocketData = RocketDataInit();
    }

    handle_rocket_command = (cmdArray: string[]) => {
        switch (cmdArray[1]) {
            case 'ballheight+':
                this.data.joules -= ballHeightUpgrade_cost(this.engine.datamap);
                this.data.ball.height = nextBallHeightUpgrade(this.data.ball.height);
                break;

            case 'ballmass+':
                this.engine.datamap.prophetData.congregation.USD -= ballMassUpgrade_cost(this.engine.datamap)
                this.data.ball.mass = nextBallMassUpgrade(this.data.ball.mass)
                break;

            case 'ballvelocity+':
                this.data.joules -= ballVelocityUpgrade_cost(this.engine.datamap);
                this.data.thrown_ball.initial_velocity = nextBallVelocityUpgrade(this.data.thrown_ball.initial_velocity);
                break;

            case 'ballbounces+':
                    this.engine.datamap.prophetData.congregation.USD -= ballBounceUpgrade_cost(this.engine.datamap);
                    this.data.bouncyball.bounces_upgrades = nextBallBounceUpgrade(this.data.bouncyball.bounces_upgrades);
                    break;

            case 'throwball':
                this.throwball();
                break;

            case 'dropball':
                this.dropball();
                break;

            case 'bounceball':
                this.bounceball();
                break;

            default:
                break;
        }
        this.updateEstimates();
    }

    dropball = () => {
        let ball = this.data.ball;

        ball.sim = {
            active: true,
            current_h: ball.height,
            current_v: 0,
            elapsedTime: 0,
            impactJouls: 0,
            mass: ball.mass,
        }
        ball.height = 0;
    }

    throwball = () => {
        let ball = this.data.ball;

        this.data.thrown_ball.sim = {
            active: true,
            current_h: ball.height  ,
            current_v: this.data.thrown_ball.initial_velocity,
            elapsedTime: 0,
            impactJouls: 0,
            mass: ball.mass,
        }

        ball.height = 0;
        this.data.thrown_ball.initial_velocity = 0;
    }

    bounceball () {
        //console.log('bounce');
        
        let ball = this.data.ball;
        let thrown_ball = this.data.thrown_ball

        this.data.bouncyball.sim = {
            active: true,
            current_h: ball.height,
            bounces: this.data.bouncyball.bounces_upgrades + 1,
            current_v: thrown_ball.initial_velocity,
            elapsedTime: 0,
            impactJouls: 0,
            mass: ball.mass,
        }

        ball.height = 0;
        this.data.thrown_ball.initial_velocity = 0;
        this.data.bouncyball.bounces_upgrades = 0;
    }

    updateEstimates() {
        let estimatedVMax = freefall_vmax_from_h(this.data.ball.height);
        let estimatedTime = freefall_t_from_h(this.data.ball.height);

        this.data.ball.derived.estimatedTime = estimatedTime
        this.data.ball.derived.estimatedVMax = estimatedVMax
        this.data.ball.derived.estimatedJouls = freefall_j_from_mv(estimatedVMax, this.data.ball.mass)
    }

    processDelta = (delta: number) => {
        if (this.data.ball.sim.active === true) this.ballsim(delta / SECOND_MS);
        if (this.data.thrown_ball.sim.active === true) this.thrownballsim(delta / SECOND_MS);
        if (this.data.bouncyball.sim.active === true) this.bounceyball_sim(delta / SECOND_MS);
    }

    vhat_sim (v_in: number, height: number, acceleration: number, seconds: number) {
        let estimatedDistance = freefall_d_from_vat(v_in, GRAVITY, seconds)
        let delta_t, delta_h, vf;

        if (estimatedDistance + height > 0) {

            delta_t = seconds;
            delta_h = estimatedDistance
            vf = freefall_vf_from_viat(v_in, acceleration, seconds)
        } else { //need to solve FROM d

            vf = freefall_vf_from_viad(v_in, GRAVITY, height);

            delta_t = freefall_t_from_vivfd(v_in, vf, height)
            delta_h = -height
        }
        return {delta_t, delta_h, vf}
    }

    move_and_impact(sim: SimData, seconds: number){
        let vin = sim.current_v;
        let h = sim.current_h;


        let {delta_t, delta_h, vf} = this.vhat_sim(vin,h,GRAVITY,seconds);

        sim.elapsedTime += delta_t;
        sim.current_h += delta_h;
        sim.current_v = vf;
        return sim.current_h <= 0
    }

    ballsim = (seconds: number) => {

        
        let ball = this.data.ball;
        let did_impact = this.move_and_impact(ball.sim, seconds);
        //console.log(ball);
        if (did_impact) {
            ball.sim.active = false;
            this.ballimpact();
        }

    }


    thrownballsim = (seconds: number) => {

        let ball = this.data.thrown_ball;
        let did_impact = this.move_and_impact(ball.sim, seconds);
        //console.log(ball);
        if (did_impact) {
            ball.sim.active = false;
            this.thrown_ballimpact();
        }


    }
    
    bounceyball_sim (seconds: number) {
        let initial_time =  this.data.bouncyball.sim.elapsedTime
        let ball = this.data.bouncyball;
        let did_impact = this.move_and_impact(ball.sim, seconds);
        //console.log(ball);
        if (did_impact) {
            ball.sim.bounces --
            if (ball.sim.bounces < 0) {
                ball.sim.active = false;
                this.bounce_ballimpact();
            } else {
                let bounce_energy = this.bounce_ballimpact();
                ball.sim.current_v = v_from_mj(ball.sim.mass, bounce_energy)
                this.bounceyball_sim(seconds - (initial_time - this.data.bouncyball.sim.elapsedTime))
            }
        }
    }

    impact() {
        let gainedJoules = freefall_j_from_mv(this.data.ball.sim.current_v, this.data.ball.mass);
        this.data.ball.sim.impactJouls = gainedJoules;
        this.data.joules += Math.floor(gainedJoules)
    }

    ballimpact() {
        let gainedJoules = freefall_j_from_mv(this.data.ball.sim.current_v, this.data.ball.mass);
        this.data.ball.sim.impactJouls = gainedJoules;
        this.data.joules += Math.floor(gainedJoules)
    }

    thrown_ballimpact() {
        let gainedJoules = freefall_j_from_mv(this.data.thrown_ball.sim.current_v, this.data.ball.mass);
        this.data.thrown_ball.sim.impactJouls = gainedJoules;
        this.data.joules += Math.floor( gainedJoules )
    }

    bounce_ballimpact() {
        let gainedJoules = freefall_j_from_mv(this.data.bouncyball.sim.current_v, this.data.ball.mass);
        this.data.bouncyball.sim.impactJouls += gainedJoules;
        this.data.joules += Math.floor( gainedJoules )
        return (gainedJoules) * .8
    }

}



//CLASS HELPERS 

export function nextBallHeightUpgrade(height: number) {
    return 1 + height;
}

export function ballHeightUpgrade_cost(data: Datamap) {
    return Math.floor(-kinetic_energy_mgdh(data.rocketData.ball.mass, 1))
}

export function ballHeightUpgrade_able(data: Datamap) {
    return ballHeightUpgrade_cost(data) <= data.rocketData.joules && data.rocketData.ball.mass > 0;
}

export function nextBallMassUpgrade(mass: number) {
    return 1 + mass;
}

export function ballMassUpgrade_cost(data: Datamap) {
    return data.rocketData.ball.mass * data.rocketData.ball.mass + 1;
}

export function ballMassUpgrade_able(data: Datamap) {
    return ballMassUpgrade_cost(data) <= data.prophetData.congregation.USD;
}

export function nextBallVelocityUpgrade(velocity: number) {
    return 2 + velocity;
}

export function ballVelocityUpgrade_cost(data: Datamap) {
    // ke = 1/2mvv
    let velocity = data.rocketData.thrown_ball.initial_velocity;
    let newv = nextBallVelocityUpgrade(velocity);
    let oldKE = .5 * data.rocketData.ball.mass * velocity * velocity;
    let newKE = .5 * data.rocketData.ball.mass * newv * newv;
    
    return newKE-oldKE; 
}

export function ballVelocityUpgrade_able(data: Datamap) {
    return ballVelocityUpgrade_cost(data) <= data.rocketData.joules;
}

export function nextBallBounceUpgrade(bounce: number) {
    return 1 + bounce;
}

export function ballBounceUpgrade_cost(data: Datamap) {
    return Math.pow(10, data.rocketData.bouncyball.bounces_upgrades+4);
}

export function ballBounceUpgrade_able(data: Datamap) {
    return ballBounceUpgrade_cost(data) <= data.prophetData.congregation.USD;
}

//ROCKET COMMANDS

export const cmd_ballheightplus: DecoupledCommand2 = {
    name: () => 'Lift Ball',
    command: () => 'rocket ballheight+',
    change: (data) => `${data.rocketData.ball.height} -> ${nextBallHeightUpgrade(data.rocketData.ball.height)}`,
    able: ballHeightUpgrade_able,
    cost: ballHeightUpgrade_cost,
    costName: 'j'
}

export const cmd_ballmassplus: DecoupledCommand2 = {
    name: () => 'Increase Ball Mass',
    command: () => 'rocket ballmass+',
    change: (data) => `${data.rocketData.ball.mass} -> ${nextBallMassUpgrade(data.rocketData.ball.mass)}`,
    able: ballMassUpgrade_able,
    cost: ballMassUpgrade_cost,
    costName: 'USD'
}

export const cmd_ballvelocityplus: DecoupledCommand2 = {
    name: () => 'Increase Initial Ball Velocity',
    command: () => 'rocket ballvelocity+',
    change: (data) => `${data.rocketData.thrown_ball.initial_velocity} -> ${nextBallVelocityUpgrade(data.rocketData.thrown_ball.initial_velocity)}`,
    able: ballVelocityUpgrade_able,
    cost: ballVelocityUpgrade_cost,
    costName: 'j',
    hidden: (data) => data.rocketData.ball.mass === 0 || data.rocketData.ball.height === 0,
}

export const cmd_ballbouncesplus: DecoupledCommand2 = {
    name: () => 'Bounces',
    command: () => 'rocket ballbounces+',
    change: (data) => `${data.rocketData.bouncyball.bounces_upgrades} -> ${nextBallBounceUpgrade(data.rocketData.bouncyball.bounces_upgrades)}`,
    able: ballBounceUpgrade_able,
    cost: ballBounceUpgrade_cost,
    hidden: (data) => data.rocketData.thrown_ball.initial_velocity === 0,
    costName: 'USD'
}

export const cmd_dropball: DecoupledCommand2 = {
    name: () => 'Drop Ball',
    command: () => 'rocket dropball',
    able: (data) => data.rocketData.ball.sim.active === false && data.rocketData.ball.height > 0,
}

export const cmd_throwball: DecoupledCommand2 = {
    name: () => 'Throw Ball',
    command: () => 'rocket throwball',
    able: (data) => data.rocketData.thrown_ball.sim.active === false,
    hidden: (data) => data.rocketData.thrown_ball.initial_velocity === 0,
}

export const cmd_bouncyball: DecoupledCommand2 = {
    name: () => 'Bounce Ball',
    command: () => 'rocket bounceball',
    able: (data) => data.rocketData.bouncyball.sim.active === false,
    hidden: (data) => data.rocketData.bouncyball.bounces_upgrades === 0,

}
/*
    {\displaystyle \Delta v=v_{\text{e}}\ln {\frac {m_{0}}{m_{f}}}=I_{\text{sp}}g_{0}\ln {\frac {m_{0}}{m_{f}}}}

where:

     v   {\displaystyle \Delta v\ } \Delta v\ is delta-v  the maximum change of velocity of the vehicle (with no external forces acting).
    m 0 {\displaystyle m_{0}} m_{0} is the initial total mass, including propellant, also known as wet mass.
    m f {\displaystyle m_{f}} m_{f} is the final total mass without propellant, also known as dry mass.
    v e = I sp g 0 {\displaystyle v_{\text{e}}=I_{\text{sp}}g_{0}} {\displaystyle v_{\text{e}}=I_{\text{sp}}g_{0}} is the effective exhaust velocity, where:

        I sp {\displaystyle I_{\text{sp}}} I_{\text{sp}} is the specific impulse in dimension of time.
        g 0 {\displaystyle g_{0}} g_{0} is standard gravity.
*/

/* PHYSICS */

//h = 1/2gt2, m

//h = 1/2vt

//v = gt, m/s

const GRAVITY = -9.8

export function freefall_t_from_h(height: number) { //return s

    let time = Math.sqrt(2 * height / -GRAVITY);

    return time;
}

export function freefall_vmax_from_h(height: number) { //return m/s
    let vmax = GRAVITY * freefall_t_from_h(height);
    return vmax;
}

export function freefall_d_from_vat(velocity_in: number, acceleration: number, seconds: number) { //return m
    let d = velocity_in * seconds + .5 * acceleration * seconds * seconds;
    return d;
}

export function freefall_vf_from_viat(velocity_in: number, acceleration: number, seconds: number) { //return m/s
    let vf = velocity_in + acceleration * seconds;
    return vf;
}

export function freefall_vf_from_viad(velocity_in: number, acceleration: number, distance: number) { //return m/s

    let vf_sq = ((velocity_in + 1) / Math.abs(velocity_in + 1)) * (velocity_in * velocity_in) + 2 * acceleration * distance;
    let vf = Math.sqrt(Math.abs(vf_sq));
    //console.log('ff_vf_viad', velocity_in, acceleration, distance, vf_sq, vf);
    if (vf_sq < 0) return -vf
    else return vf
}

export function freefall_t_from_vivfd(v_i: number, v_f: number, distance: number) { //return s
    let t = 2 * distance / (v_i + v_f)
    return Math.abs(t)
}

export function freefall_j_from_mv(v: number, m_kg: number, ) { //return j 
    return .5 * m_kg * v * v
}

export function v_from_mj (mass: number, joules: number) {
    let velocity = Math.sqrt( 2 * joules / mass )
    return velocity;
}

export function kinetic_energy_mgdh (mass: number, delta_h: number) {
    return mass * delta_h * GRAVITY;    
}