import React from 'react';
import AppContext from '../../context/context';
import CommandButtonFC from './CommandButtonFC';
import { RELICS, relic_canBuy, relic_getCost, relic_getTime, relic_getProductionPerecond, relic_getProductionMulti, relic_maxBuyAmount, relic_canBuyNext, relic_buyNextCost, relic_getProduction, Building_Multi, relic_maxBuyCost } from '../../engine/modules/Prophet';
import ProgressFC from './ProgressFC';
import TipFC from './TipFC';
import { relicUSDupgrade_able, relicUSDupgrade_cost } from '../../engine/modules/USDRelicPurchases';
import RelicTip from './RelicTip';
import { formatNumber } from '../../engine/util';


const RelicBox: React.FC<{ relicIndex: number }> = (props) => (
  <AppContext.Consumer>
    {({ data, submitCommand }) => (
      <div hidden={props.relicIndex > 0 && data.prophetData.relics[props.relicIndex - 1] === 0} className='RelicBox'>
        <ProgressFC background={'yellow'} color='green' value={data.prophetData.relicTimers[props.relicIndex]} max={relic_getTime(props.relicIndex)} 
          label={`${RELICS[props.relicIndex].name} x${data.prophetData.relics[props.relicIndex]}`}
          />
        <div className='relicboxrow'>

          <div hidden={data.prophetData.relicsetbuy !== 1}>

            <CommandButtonFC name={`Make`} command={`prophet buyrelic ${props.relicIndex}`} able={(datamap) => { return relic_canBuy(props.relicIndex, datamap) }}
              tip={<RelicTip relicIndex={props.relicIndex}/>}
            />
          </div>
            
          <div hidden={data.prophetData.relicsetbuy !== 99}>
            <CommandButtonFC name={`Make Max (${relic_maxBuyAmount(props.relicIndex, data)})`} command={`prophet buymaxrelic ${props.relicIndex}`} 
              able={(datamap) => { return relic_maxBuyAmount(props.relicIndex, data) > 0 }}
              tip={<RelicTip relicIndex={props.relicIndex}/>}
             />
          </div>
            
          <div hidden={data.prophetData.relicsetbuy !== 25}>

            <CommandButtonFC name={`Make to 25x`} command={`prophet buyto25relic ${props.relicIndex}`} able={(datamap) => { return relic_canBuyNext(props.relicIndex, data, 25) }}
              tip={<RelicTip relicIndex={props.relicIndex}/>}
            />
            </div>
            <div className='PietyDiv' hidden={data.prophetData.relicsetbuy !== 1}>
            Cost: {formatNumber( relic_getCost(props.relicIndex, data))}
            </div>
            <div className='PietyDiv' hidden={data.prophetData.relicsetbuy !== 99}>
            Cost: {formatNumber(Math.max(relic_maxBuyCost(props.relicIndex, data),   relic_getCost(props.relicIndex, data)  ))}
          </div>
            <div className='PietyDiv' hidden={data.prophetData.relicsetbuy !== 25}>
            Cost: {formatNumber(relic_buyNextCost(props.relicIndex, data,25))}
          </div>
            <div className='USDCommand' hidden={data.prophetData.congregation.paritioners[1] === 0}>
              <CommandButtonFC
                name={`Upgrade`}
                command={`prophet relicupgrade ${props.relicIndex}`}
                hidden={(datamap) => { return datamap.prophetData.congregation.paritioners[1] === 0 }}
                able={(datamap) => { return relicUSDupgrade_able(datamap, props.relicIndex) }}
                tip={<div>
                  Level: {data.prophetData.relicUSDUpgrades[props.relicIndex]}<br />
                  <div className='USDDiv'>
                    Cost: {formatNumber(relicUSDupgrade_cost(data, props.relicIndex))} USD
              </div>
                  +0.1 to production mult per 25 relics<br />
                {`x${Building_Multi(data, props.relicIndex)} production per 25 relics`}

                </div>}
              />
            </div>


          
          

          <div className='PietyDiv'>
            <div>
              Total P/S: {formatNumber(relic_getProductionPerecond(props.relicIndex, data))}
            </div>
            <TipFC tip={<div>
              Total Production: {formatNumber(relic_getProduction(props.relicIndex, data))}
            </div>} />
          </div>
        </div>
      </div>
    )}
  </AppContext.Consumer>
);

export default RelicBox;