export const USD_UpgradeTithe: ExpoCommand = {
    name: `Invest in Tithe Extraction`,
    command: `prophet usdupgrade tithe`,
    currencyName: `USD`,
    
    countFns: {
        get: (data) => data.prophetData.congregation.USDUpgrades.tithe,
        gain: (data, ammount) => { data.prophetData.congregation.USDUpgrades.tithe += ammount }
    },
    expo: { initial: 100, coefficient: 15 },
    currencyFns: CurrencyFn_USD,
    tip: `Increase Effectiveness of Wealthy Believers`,

    hidden: (data) => data.prophetData.congregation.paritioners[1] === 0,
}

