export default interface ExpoCurve {
    coefficient: number,
    initial: number,
}

export function expoI_getCost(expo: ExpoCurve, owned:number) {

    
    return Math.floor(expo.initial*Math.pow(expo.coefficient,owned))
}

export function expoI_maxBuyAmount (expo: ExpoCurve, owned:number, currency: number) {
    let b = expo.initial;
    let r = expo.coefficient;
    let k = owned
    let c = currency

    let logresult = Math.log(1+((c*(r - 1))/(b*Math.pow(r, k))))
    let logchange = logresult / Math.log(r);
    let result = Math.floor(logchange)

    return result;
}  

export function expoI_maxBuyCost (expo: ExpoCurve, owned:number, currency: number) {

    let n = expoI_maxBuyAmount(expo, owned, currency);
    
    return expoI_buyNCost(expo, owned, n)
}

export function expoI_buyNCost (expo: ExpoCurve, owned: number, n: number) {
    let b = expo.initial;
    let r = expo.coefficient;
    let k = owned

    return Math.floor( b * (Math.pow(r,k)*(Math.pow(r,n)-1)) / (r - 1) )
}

export function expoI_buyNextCost (expo: ExpoCurve, owned: number, next: number) {
    let k = owned;
    let n = next - k % next;

    return expoI_buyNCost(expo, owned, n)
}

export interface ExpoObject {
    [key:string]:ExpoCurve,
}