import CoreEngine from "./CoreEngine";
import ProphetModule from "./modules/Prophet";
import GameTime from "./modules/Gametime";
import NavArea from "./modules/NavArea";
import Followers from "./modules/Followers";
import { MINUTE_MS } from "./util";
import Rocket from "./modules/Rocket";
import NameAndCode from "./modules/NameAndCode";
import Adornments from "./modules/Adornments";
import { doPantheonUpgrade } from "./modules/Pantheon/Types";
import { newDefaultMap } from "./Datamap";
import _ from "lodash";

export default class Engine extends CoreEngine {
    
    processDelta = (delta: number) => {
        this.rocket.processDelta(delta)
        /*
        if (this.datamap.nacData.name === 'DEVC') {
            delta = delta * 10;
        }*/
        //console.log('tick');
        this.prophet.processDetla(delta)
        
        this.gameTime.processDelta(delta)
        this.bounds();
        this.stopOffline();

    }

    bounds () {
        const PIETYBOUND = 1e100
        if (this.datamap.prophetData.piety> PIETYBOUND) this.datamap.prophetData.piety = PIETYBOUND;
    }


    
    startOffline (delta: number) {
        this.datamap.offline = {happening: true,totaltime: delta}
        this.notify();
    }

    stopOffline () {
        this.datamap.offline.happening = false;
    }

    executeCommand = (command: string) => {
        let commands = command.split(' ');
        //console.log(`process ${command}, ${commands}`);

        switch (commands[0]) {
            case '1hr':
                this.processDelta(60 * 60 * 1000);
                break;

            case 'prophet':
                this.prophet.processCommands(commands)
                break;

            case 'gametime':
                this.gameTime.processCommands(commands);
                break;

            case 'pantheon':
                doPantheonUpgrade(this.datamap, commands)
                break;

            case 'save':
                this.save();
                break;

            case 'load':
                this.load();
                break;

            case 'import':
                this.import(commands[1]);
                break;
            
            case 'export':
                this.export();
                break;

            case 'nav':
                if (commands[1] === 'main') {
                    this.nav.changeMainArea(commands[2])
                } else if (commands[1] === 'sub') {
                    this.nav.changeSubArea(commands[2])
                }
                break;

            case 'followers':
                this.followers.handle_followers_command(commands)
                break;

            case 'rocket':
                this.rocket.handle_rocket_command(commands);
                break;
            case 'keypress':
                this.handleKey(commands[1]);
                break;

            case 'nac':
                this.nac.nacCommand(commands)
                break;

            case 'adornments':
                this.adornments.handle_adornments_command(commands);
                break;
       
            default:
                console.log('...the dev made a mistake.')
                break;

        }

        this.notify();
    }

    handleKey = (key: string) => {
        switch (key) {
            case 'p':
                this.prophet.pray();
                break;

            case 'm':
                for (let index = 0; index < 10; index++) {                    
                    this.prophet.buyMaxRelic(index.toString())
                }
                break;

            case 'r':
                this.prophet.prestige();
                break;
        
            default:
                break;
        }
    }

    load = () => {
        this.quietLoad();
        this.startOffline(Date.now()-this.datamap.last)
        this.notify();
    }

    import = (lzutf8str: string) => {
        var LZUTF8 = require('lzutf8');
        
        //console.log(lzutf8str);
        let arrayStr = lzutf8str.split(',')
        let arrayInt = arrayStr.map(value=>parseInt(value))
        let magicArray = Uint8Array.of(...arrayInt);
        

        //var string = new TextDecoder("utf-8").decode(lzutf8str);
        //let test = new Uint8Array(lzutf8str)
        //let uint8array: Uint8Array = new Uint8Array(lzutf8str)
        //let uint8array = new Uint8Array()
        //var uint8array = new TextEncoder().encode(lzutf8str);
        //console.log(uint8array);

        var output = LZUTF8.encodeUTF8(lzutf8str);
        //console.log(output, arrayStr, magicArray);
        
        let check = LZUTF8.decompress(magicArray);
        //console.log(check);

        /**
         * 
      */
        
        

        let combstr = check;
        if (combstr != null){
            let oldobj = JSON.parse(combstr);
            this.datamap = newDefaultMap();
            _.merge(this.datamap, oldobj)
        }

    }

    export = () => {
        let dataString = JSON.stringify(this.datamap);
        var LZUTF8 = require('lzutf8');
        let output = LZUTF8.compress(dataString);
        //  console.log(output)
        //console.log(dataString.length)

//        download(`prophet_export_${Date.now()}.txt`,dataString)
        download(`prophet_export_${Date.now()}.txt`,output)

        function download(filename: string, text: string) {
            var element = document.createElement('a');
            element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(text));
            element.setAttribute('download', filename);
          
            element.style.display = 'none';
            document.body.appendChild(element);
          
            element.click();
          
            document.body.removeChild(element);
          }

    }

    prophet = new ProphetModule(this);
    gameTime = new GameTime(this);
    nav = new NavArea(this);
    followers = new Followers(this)
    rocket = new Rocket(this);
    nac = new NameAndCode(this);
    adornments = new Adornments(this);
}

