
import ExpoCurve, { expoI_buyNextCost, expoI_maxBuyCost, expoI_maxBuyAmount, expoI_getCost, expoI_buyNCost } from "../interfaces/ExpoCurve";

import { Datamap } from "../Datamap";

export const RelicUSDUpgradeExpos: ExpoCurve[] = [
    { coefficient: 3.0, initial: 150 },
    { coefficient: 2.8, initial: 140 },
    { coefficient: 2.6, initial: 130 },
    { coefficient: 2.4, initial: 120 },
    { coefficient: 2.2, initial: 110 },
    { coefficient: 2.0 , initial: 1000 },
    { coefficient: 1.8, initial: 2000 },
    { coefficient: 1.6, initial: 3000 },
    { coefficient: 1.4, initial: 4000 },
    { coefficient: 1.2, initial: 5000 },
]

export function relicUSDupgrade_cost(data: Datamap, index: number) {
    let owned = data.prophetData.relicUSDUpgrades[index];
    return expoI_getCost(RelicUSDUpgradeExpos[index], owned)
}

export function relicUSDupgrade_able(data: Datamap, index: number) {
    return relicUSDupgrade_cost(data, index) <= data.prophetData.congregation.USD
}
export function relicUSDupgrade_maxBuyAmount(index: number, data: Datamap) {
    return expoI_maxBuyAmount(RelicUSDUpgradeExpos[index], data.prophetData.relicUSDUpgrades[index], data.prophetData.congregation.USD)
}

export function relicUSDupgrade_maxBuyCost(index: number, data: Datamap) {
    return expoI_maxBuyCost(RelicUSDUpgradeExpos[index], data.prophetData.relicUSDUpgrades[index], data.prophetData.congregation.USD)
}

export function relicUSDupgrade_buyNCost(index: number, data: Datamap, n: number) {
    return expoI_buyNCost(RelicUSDUpgradeExpos[index], data.prophetData.relicUSDUpgrades[index], n)
}

export function relicUSDupgrade_buyNextCost(index: number, data: Datamap, next: number) {
    return expoI_buyNextCost(RelicUSDUpgradeExpos[index], data.prophetData.relicUSDUpgrades[index], next)
}

export function relicUSDupgrade_canBuyN(index: number, data: Datamap, n: number) {
    let cost = relicUSDupgrade_buyNCost(index, data, n);
    return data.prophetData.congregation.USD >= cost
}

export function relicUSDupgrade_canBuyNext(index: number, data: Datamap, next: number) {
    let k = data.prophetData.relicUSDUpgrades[index];
    let n = next - k % next;
    return relicUSDupgrade_canBuyN(index, data, n);
}

export function relicUSDupgrade_buyHalfMax_amount(index: number, data: Datamap) {
    let full = relicUSDupgrade_maxBuyAmount(index, data);
    let half = Math.floor(full/2);
    return half;
}

export function relicUSDupgrade_buyHalfMax_cost(index: number, data: Datamap) {
    let half = relicUSDupgrade_buyHalfMax_amount(index, data);
    return relicUSDupgrade_buyNCost(index, data, half)
}