import AbstractModule from "./AbstractModule";
import { Datamap } from "../Datamap";
import ExpoCurve, { ExpoObject, expoI_getCost } from "../interfaces/ExpoCurve";

export default class Exploring extends AbstractModule {

    sendRuins() {
        if (this.data.areas.ruins.beingExplored || this.data.readyForMission === 0) return;
        this.data.areas.ruins.assignedExplorers = this.data.readyForMission;
        this.data.areas.ruins.beingExplored = true;
        this.data.readyForMission = 0;
    }

    sendMart() {
        if (this.data.areas.mart.beingExplored || this.data.readyForMission === 0) return;
        this.data.areas.mart.assignedExplorers = this.data.readyForMission;
        this.data.areas.mart.beingExplored = true;
        this.data.readyForMission = 0;
    }

    sendShop() {
        if (this.data.areas.shop.beingExplored || this.data.readyForMission === 0) return;
        this.data.areas.shop.assignedExplorers = this.data.readyForMission;
        this.data.areas.shop.beingExplored = true;
        this.data.readyForMission = 0;
    }

    stopRuins() {
        this.data.areas.ruins.assignedExplorers = 0;
        this.data.areas.ruins.beingExplored = false;
    }
    
    stopMart() {
        this.data.areas.mart.assignedExplorers = 0;
        this.data.areas.mart.beingExplored = false;
    }

    stopShop() {
        this.data.areas.shop.assignedExplorers = 0;
        this.data.areas.shop.beingExplored = false;
    }

    hireExplorer ( ) {
        if (can_hire(this.engine.datamap) === false) return;
        this.data.explorerCount ++;
    }

    fireExplorer () {
        if (this.data.explorerCount > 0) {
            this.data.explorerCount --;
            this.engine.datamap.prophetData.believers --;
        }
    }

    get data () {
        return this.engine.datamap.adornments.explorers;
    }
 
    handle_explore_command = (cmdArray: string[]) => {
        switch (cmdArray[2]) {
            case 'hire':
                this.hireExplorer();
                break;

            case 'fire':
                this.fireExplorer();
                break;
            
            case 'sendup':
                this.sendup();
                break;

            case 'senddown':
                this.senddown();
                break;

            case 'ruins':
                this.sendRuins();
                break;
            
            case 'mart':
                this.sendMart();
                break;

            case 'shop':
                this.sendShop();
                break;

                case 'stopruins':
                this.stopRuins();
                break;
            
            case 'stopmart':
                this.stopMart();
                break;

            case 'stopshop':
                this.stopShop();
                break;
            
                default:
                break;
        }
    }

    exploreDay = () => {

        this.checkBelievers();
        this.checkSalary();
        this.checkOut();

        this.checkReady();

        this.exploreProgress();
    }

    checkBelievers () {
        if (this.engine.datamap.prophetData.believers < this.data.explorerCount) {
            this.data.explorerCount = this.engine.datamap.prophetData.believers;
        }
    }

    checkReady () {
        if (availableExplorers(this.engine.datamap) < this.data.readyForMission) {
            this.data.readyForMission = availableExplorers(this.engine.datamap);
        }
    }

    checkOut () {
        if (availableExplorers(this.engine.datamap) < 0) {
            this.stopRuins();
            this.stopMart();
            this.stopShop();
        }
    }


    

    checkSalary () {
        if (this.engine.datamap.prophetData.congregation.USD < currentSalary(this.engine.datamap)) {
            this.engine.datamap.prophetData.congregation.USD = 0;
            this.engine.datamap.prophetData.believers -= this.data.explorerCount;
            this.data.explorerCount = 0;
        } else {
            this.engine.datamap.prophetData.congregation.USD -= currentSalary(this.engine.datamap);
        }
    }

    sendup () {
        this.data.readyForMission = Math.min(availableExplorers(this.engine.datamap), this.data.readyForMission + 1)
    }

    senddown () {
        this.data.readyForMission = Math.max(0, this.data.explorerCount - 1)
    }

    exploreProgress () {
        let areas = this.data.areas;

        [areas.ruins, areas.mart, areas.shop].forEach(area => {
            if (area.beingExplored) {
                area.investedDays += area.assignedExplorers;
            }
        });
        this.checkForFinshedAreas();
    }

    checkForFinshedAreas = () => {
        this.exploreRuins();
        this.exploreMart();
        this.exploreShop();
    }

    checkArea(area: AreaData, expo: ExpoCurve) {
        let goal = getAreaGoalDirct(area,expo)
        if (area.investedDays >= goal) {
            area.investedDays -= goal;
            area.exploredCount ++;
            return true;
        } else return false;
    }

    exploreRuins () {
        let area = this.data.areas.ruins;
        let expo = AreaExpos.ruins;
        let key = 'ruins';
        
        let done = this.checkArea(area,expo);
        if (done) {
            this.engine.adornments.getItem(key, area.exploredCount);
        }
    }

    exploreMart () {
        let area = this.data.areas.mart;
        let expo = AreaExpos.mart;
        let key = 'mart';
        
        let done = this.checkArea(area,expo);
        if (done) {
            this.engine.adornments.getItem(key, area.exploredCount);
        }
    }

    exploreShop () {
        let area = this.data.areas.shop;
        let expo = AreaExpos.shop;
        let key = 'shop';
        
        let done = this.checkArea(area,expo);
        if (done) {
            this.engine.adornments.getItem(key, area.exploredCount);
        }
    }

    reset = () => {
        this.engine.datamap.adornments.explorers = ExplorersData_init();
    }

}

const EXPLORER_SALARY = 10;

export const AreaExpos: ExpoObject = {
    ruins: { coefficient: 4, initial: 365 },
    mart: { coefficient: 2, initial: 365 * 10 },
    shop: { coefficient: 3, initial: 365 * 100 },
}

export function getAreaGoal (area: AreaData, areaExpoKey: string) {
    return expoI_getCost(AreaExpos[areaExpoKey], area.exploredCount)
}

export function getAreaETA (area: AreaData, areaExpoKey: string) {
    let goal = expoI_getCost(AreaExpos[areaExpoKey], area.exploredCount)
    let current = area.investedDays;
    let remainder = goal - current;
    let rate = area.assignedExplorers;
    return Math.ceil(remainder/rate)
}

export function getAreaGoalDirct (area: AreaData, areaExpo: ExpoCurve) {
    return expoI_getCost(areaExpo, area.exploredCount)
}



export function can_hire (data: Datamap) {
    return data.prophetData.believers > data.adornments.explorers.explorerCount;
}

export function availableExplorers (data:Datamap) {
    let explorData = data.adornments.explorers;
    return explorData.explorerCount - explorData.areas.ruins.assignedExplorers - explorData.areas.shop.assignedExplorers - explorData.areas.mart.assignedExplorers
}

export function currentSalary (data:Datamap) {
    let val = data.adornments.explorers.explorerCount * EXPLORER_SALARY
    if (data.pantheon.wealth.c > 0) {
        val = val * Math.pow(.95, data.pantheon.wealth.c)
    } 
    return Math.floor(val);
    
}

export interface AreaData {
    exploredCount: number,
    investedDays: number,
    assignedExplorers: number,
    beingExplored: boolean,
}

export function AreaData_init (): AreaData {
    return {
        exploredCount: 0,
        investedDays: 0,
        assignedExplorers: 0,
        beingExplored: false,
    }

}


export interface ExplorersData {
    explorerCount: number;
    readyForMission: number;
    areas: {
        ruins: AreaData;
        shop: AreaData;
        mart: AreaData;
    }
}

export function ExplorersData_init(): ExplorersData {
    return {
        explorerCount: 0,
        readyForMission: 0,
        areas: {
            ruins: AreaData_init(),
            shop: AreaData_init(),
            mart: AreaData_init(),
        }
    }
}

export function show_explore_area (data:Datamap, areaKey: string) {
    switch (areaKey) {
        case 'mart':
            return getAreaGoalDirct(data.adornments.explorers.areas.ruins, AreaExpos.ruins) > AreaExpos.mart.initial;
            break;

        case 'shop':
            return getAreaGoalDirct(data.adornments.explorers.areas.mart, AreaExpos.mart) > AreaExpos.shop.initial;
            break;
    
        default:
            return true
            break;
    }
}

const RUINS_STRS = {
    key: 'ruins',
    name: 'Ruins'
}

const MART_STRS = {
    key: 'mart',
    name: 'Mart'
}

const SHOP_STRS = {
    key: 'shop',
    name: 'Shop'
}

export const AREA_STRS = {
    RUINS_STRS, MART_STRS, SHOP_STRS
}