import AppContext from "../../context/context";

import './PantheonUI.scss'

import React from "react";
import CommandButtonFC from "./CommandButtonFC";
import { BasePantheonUpgrade, pantheonPurchase_able, pantheonPurchaseCost, pantheonPurchase_maxCost, pantheonPurchase_maxAmmount } from "../../engine/modules/Pantheon/Types";
import CommandButton2 from "./CommandButton2";
import * as TIME from '../../engine/modules/Pantheon/Time';
import TipFC from "./TipFC";
import Pantheons from '../../engine/modules/Pantheon/Pantheons';
import { formatNumber } from "../../engine/util";

const PantheonUI: React.FC = () => (
    <AppContext.Consumer>
        {({data, submitCommand}) => (
            <div className='PantheonUI'>
                <span className='HeaderText'>
                    Pantheon
                </span>
                <div>
                    Reincarnate to choose new gods for your Pantheon. Upgrades, once bought, persist through reincarnations even before you add them to your pantheon.
                </div>
                <div>
                
                    <TipFC tip={`Not 100% usefull yet. Only the first tier of god (Proud) has additional upgrades. Feel free to suggest upgrades for the missing ones.`}/>

                </div>
                <div className='pantheoncolumn'>

                {data.prophetData.devotionUpgrades.abc.a && <div>
                    <span className='HeaderText'>
                        God of Time
                        </span>
                        <div>
                    <PantheonUpgradeComp dp={TIME.Upgrade_A} />
                    <PantheonUpgradeComp dp={TIME.Upgrade_B} />
                    <PantheonUpgradeComp dp={TIME.Upgrade_C} />
                    <div>
                        {data.pantheon.time.bonusDays}   
                    </div>
                        </div>
                    </div>}
                {data.prophetData.devotionUpgrades.abc.b && <div>
                    <span >
                    God of Production
                        </span>
                        <div>
                    <PantheonUpgradeComp dp={Pantheons.PRODUCTION.Upgrade_A} />
                    <PantheonUpgradeComp dp={Pantheons.PRODUCTION.Upgrade_B} />
                    <PantheonUpgradeComp dp={Pantheons.PRODUCTION.Upgrade_C} />
                        </div>
                    </div>}
                {data.prophetData.devotionUpgrades.abc.c && <div>
                    <span className='HeaderText'>
                    God of Wealth
                        </span>
                        <div>
                    <PantheonUpgradeComp dp={Pantheons.WEALTH.Upgrade_A} />
                    <PantheonUpgradeComp dp={Pantheons.WEALTH.Upgrade_B} />
                    <PantheonUpgradeComp dp={Pantheons.WEALTH.Upgrade_C} />
                        </div>
                    </div>}
                {data.prophetData.devotionUpgrades.def.a && <div>
                    <span className='HeaderText'>
                    God of Persuasion
                    </span>
                    Nothing Here! <a target="_blank" href='http://www.reddit.com/r/IA_Games/'>What do you want to see?</a>
                    </div>}
                {data.prophetData.devotionUpgrades.def.b && <div>
                    <span className='HeaderText'>
                    God of Maintainance
                        </span>
                    Nothing Here! <a target="_blank" href='http://www.reddit.com/r/IA_Games/'>What do you want to see?</a>
                    </div>}
                {data.prophetData.devotionUpgrades.def.c && <div>
                    <span className='HeaderText'>
                    God of Greed
                        </span>
                    Nothing Here! <a target="_blank" href='http://www.reddit.com/r/IA_Games/'>What do you want to see?</a>
                    </div>}
                </div>
            </div>
        )}
    </AppContext.Consumer>
)

export default PantheonUI;


const PantheonUpgradeComp: React.FC<{ dp: BasePantheonUpgrade }> = (props) => (
    <AppContext.Consumer>
        {({ data }) => (
            <div className='PietyCommand flexcolumn pantheonupgrade'>
            <CommandButton2 {...props.dp.command}
                cost={(data)=>pantheonPurchaseCost(data,props.dp)}
                able={(data)=>pantheonPurchase_able(data, props.dp)}
                costName='Piety'
                tip={<div>
                    Cost: {formatNumber(pantheonPurchaseCost(data, props.dp))}<br />
                    {props.dp.description}<br />
                    Level : {props.dp.countFn(data)}
                </div>}
            />
            <CommandButton2 name={()=>`Buy Max (${pantheonPurchase_maxAmmount(data,props.dp)})`} command={(data)=>props.dp.command.command(data)+'max'} 
                        able={(data)=>pantheonPurchase_able(data, props.dp)}
                        />
            </div>
        )}
    </AppContext.Consumer>
)
