import AppContext from "../../context/context"

import CommandButtonFC from "./CommandButtonFC"

import { CMD_prophet_pray, prophet_maxHolyPower, relic_hoylPowerMulti, holiness_mult, reincation_mult, special_relic_mult, time_relic_mult, reincation_mult_from_superPrestiges, item_relic_mult, relicDeltaMult } from "../../engine/modules/Prophet"

import { piety_purchase_hide_holypower, piety_purchase_canBuy_holypower, holyPowerCost, piety_purchase_hide_reincarnation, piety_purchase_canBuy_reincarnation, reincarnationCost, piety_purchase_hide_congregation, piety_purchase_canBuy_congregation, congregationCost, UnlockMiracles } from "../../engine/modules/PietyPurchases"

import RelicBox from "./RelicBox"

import ProgressFC from "./ProgressFC"

import { devotion_mult } from "../../engine/modules/Followers"

import TipFC from "./TipFC"
import React from "react"
import StatFC from "./StatFC"
import CommandButton2 from "./CommandButton2"

const RelicUI: React.FC = () => (
    <AppContext.Consumer>
        {({ data }) => {return(
            <div className='RelicUI'>

                <div className='flexrow'>

                    <div className='flexcolumn flexgrow'>

                        <div className='HeaderText'>Commands</div>

                        <div className='ProphetCommands'>
                            <CommandButtonFC {...CMD_prophet_pray} tip={<div>Pray to increase your piety.</div>} />

                            <CommandButtonFC name='Empowering Prayer' command='prophet holypower'
                                hidden={piety_purchase_hide_holypower}
                                able={piety_purchase_canBuy_holypower}
                                tip={<div className='PietyDiv'>Cost: {holyPowerCost}</div>}
                            />
                            <CommandButtonFC name='Blessing of Reincarnation' command='prophet reincarnate'
                                hidden={piety_purchase_hide_reincarnation}
                                able={piety_purchase_canBuy_reincarnation}
                                tip={<div className='PietyDiv'>Cost: {reincarnationCost}</div>}
                            />
                            <CommandButtonFC name='Create Congregation!' command='prophet congregate'
                                hidden={piety_purchase_hide_congregation}
                                able={piety_purchase_canBuy_congregation}
                                tip={<div className='PietyDiv'>Cost: {congregationCost}</div>}
                            />
                        </div>

                        <div className='flexrow spacebetween'>
                        <div className='HeaderText'>Relics</div>
                        <div className='flexrow'>

                        Make:
                        <div>
                                <CommandButtonFC
                                    name='1'
                                    command='prophet setbuyrelic 1'
                                    />
                        </div>
                                <CommandButtonFC
                                    name='x25'
                                    command='prophet setbuyrelic 25'
                                />
                                <CommandButtonFC
                                    name='Max'
                                    command='prophet setbuyrelic max'
                                    />
                        </div>
                                    </div>

                        <div className='flexrow'>

                        <div className='flexcolumn flexequal flexgrow'>

                            <RelicBox relicIndex={0} />
                            <RelicBox relicIndex={1} />
                            <RelicBox relicIndex={2} />
                            <RelicBox relicIndex={3} />
                            <RelicBox relicIndex={4} />
                        </div>
                        {(data.prophetData.relics[4] != 0)&&
                        <div className='flexcolumn flexequal flexgrow'>
                            <RelicBox relicIndex={5} />
                            <RelicBox relicIndex={6} />
                            <RelicBox relicIndex={7} />
                            <RelicBox relicIndex={8} />
                            <RelicBox relicIndex={9} />
                        </div>}
                        </div>

                    </div>

                    <div className='flexcolumn'>
                        <div className='HeaderText'>Relic Production Multipliers</div>
                        <div className='multbox'>
                            <div hidden={data.prophetData.holyPowerUnlocked === false} >

                                <div hidden={data.prophetData.holyPowerUnlocked === false} className='HolyPowerWrapper'>
                                    <ProgressFC max={prophet_maxHolyPower(data)}
                                        label={<div className='flexrow spacebetween'>
                                            <div>
                                            {`Holy Power (${Math.floor(100 * data.prophetData.holyPower / prophet_maxHolyPower(data))}%) `} 
                                            </div>
                                            <div>
                                            {` x${relic_hoylPowerMulti(data)}`}
                                            </div>
                                        </div>}
                                        value={data.prophetData.holyPower}
                                        tip={<div>
                                            Make more relics to increase your max Holy Power!
                                        </div>}
                                    />
                                </div>
                            </div>
                            <div hidden={data.prophetData.holiness === 0} className='HolinessDiv'>
                                <StatFC value={holiness_mult(data)} units="x" name='Holiness' />
                            </div>
                            <div hidden={data.prophetData.devotion === 0} className='DevotionDiv'>
                                <StatFC value={devotion_mult(data)} units="x" name='Devotion' />
                            </div>
                            <div hidden={data.prophetData.save.reincarnationCount === 0}>
                                <StatFC value={reincation_mult(data)} units="x" name='Reincarnation'/>
                                {data.followers.devotedPrestige > 0 && <TipFC tip={<div>
                                    {`x${reincation_mult_from_superPrestiges(data)} from Divine Reincarnations`}
                                </div>} />}
                            </div>
                            
                            <div hidden={special_relic_mult(0, data) === 1}>
                            <StatFC value={special_relic_mult(0,data)} units="x" name='Special Mult(0)'/>                
                            <StatFC value={special_relic_mult(9,data)} units="x" name='Special Mult(9)'/>                
                                <TipFC tip={<div>

                                    <div>
                                        {`Base: x1`}
                                    </div>
                                    <div hidden={data.followers.DPUpgrades.monthMultiplier + data.followers.DPUpgrades.yearMultiplier === 0}>
                                        {`Time Based Mult: +x${time_relic_mult(data).toFixed(1)}`}
                                    </div>
                                </div>}
                                />
                            </div>
                            {item_relic_mult(0,data)>1 && <StatFC value={item_relic_mult(0,data)} units='x' name='Item' />}
                        </div>
                        <div className='HeaderText'>
                            Info
                        </div>
                                {<StatFC value={relicDeltaMult(data)} units="x" name='Relic Production Speed'/>}                
                        

                    </div>

                </div>





            </div>
        )}}
    </AppContext.Consumer>
)


export default RelicUI;