import { Datamap } from "../Datamap";
import { DecoupledCommand } from "../interfaces/ICommand";
import AbstractModule from "./AbstractModule";
import ExpoCurve, { expoI_getCost, expoI_maxBuyAmount, expoI_maxBuyCost, expoI_buyNCost, expoI_buyNextCost } from "../interfaces/ExpoCurve";
import { helper_prophet_exBeliever_harm, availablePeople } from "./Prophet";
import { doDevotionUpgrade } from "./DevotionPurchases";

export default class Followers extends AbstractModule {


    get data() {
        return this.engine.datamap.followers;
    }

    handle_followers_command = (cmdArray: string[]) => {
        switch (cmdArray[1]) {
            case 'recruit':
                this.handle_recruit_command(cmdArray)
                break;

            case 'upgrade':
                this.handle_upgrade_command(cmdArray)
                break;

            case 'recruitparitioner':
                this.recruitParitioner(cmdArray[2]);
                break;

            case 'recruitmaxparitioner':
                this.recruitMaxParitioner(cmdArray[2]);
                break;

            case 'recruithalfmaxparitioner':
                this.recruitHalfMaxParitioner(cmdArray[2]);
                break;

            case 'unlock':
                this.handle_unlock_command(cmdArray);
                break;

            case 'divinity':
                this.handle_divinity_command(cmdArray);
                break;

            case 'devotionupgrade':
                doDevotionUpgrade(this.engine.datamap, cmdArray[2])
                break;


            default:
                break;
        }
    }

    handle_divinity_command = (cmdArray: string[]) => {
        let data = this.engine.datamap;

        Object.values(DP_Upgrades).forEach(dp => {
            if (cmdArray[2] === dp_key(dp)) {
                buy_divinityPurchase(data, dp)
            }
        })

        if (cmdArray[2] === 'reset_toggle') {
            this.data.DPUpgrades.reset = !this.data.DPUpgrades.reset
        }

    }

    handle_recruit_command = (cmdArray: string[]) => {
        switch (cmdArray[2]) {
            case 'preacher':
                this.recruit_preacher()
                break;
            case 'devotee':
                this.recruit_devotee()
                break;
            case 'doorknocker':
                this.recruit_doorknocker()
                break;
            case 'preachermax':
                this.recruit_preacher_max()
                break;
            case 'devoteemax':
                this.recruit_devotee_max()
                break;
            case 'doorknockermax':
                this.recruit_doorknocker_max()
                break;
            case 'autotoggle':
                this.engine.datamap.automation.auto_buyToNext = !this.engine.datamap.automation.auto_buyToNext
            break;

            default:
                break;
        }
    }

    handle_upgrade_command = (cmdArray: string[]) => {
        switch (cmdArray[2]) {
            case 'preacher':
                this.upgrade_preacher()
                break;

            case 'devotee':
                this.upgrade_devotee();
                break;

            case 'doorknocker':
                this.upgrade_doorknocker();
                break;

            default:
                break;
        }
    }

    handle_unlock_command = (cmdArray: string[]) => {
        switch (cmdArray[2]) {
            case 'adornments':
                this.unlockAdornments();
                break;

            default:
                break;
        }
    }

    recruit_preacher_max = () => {
        while (preacher_cmd_able(this.engine.datamap) && (this.engine.datamap.prophetData.congregation.preachers < this.engine.datamap.prophetData.congregation.old.preachers)) {
            this.recruit_preacher();
        }
        while (preacher_upgrade_cmd_able(this.engine.datamap) && (this.engine.datamap.prophetData.congregation.preacherLevel < this.engine.datamap.prophetData.congregation.old.preacherLevel)) {
            this.upgrade_preacher();
        }
    }

    recruit_doorknocker_max = () => {
        while (doorknocker_cmd_able(this.engine.datamap) && (this.engine.datamap.prophetData.congregation.doorknocker < this.engine.datamap.prophetData.congregation.old.doorknocker)) {
            this.recruit_doorknocker();
        }
        while (doorknocker_upgrade_cmd_able(this.engine.datamap) && (this.engine.datamap.prophetData.congregation.doorknockerLevel < this.engine.datamap.prophetData.congregation.old.doorknockerLevel)) {
            this.upgrade_doorknocker();
        }
    }

    recruit_devotee_max = () => {
        while (devotee_cmd_able(this.engine.datamap) && (this.engine.datamap.prophetData.congregation.devotee < this.engine.datamap.prophetData.congregation.old.devotee)) {
            this.recruit_devotee();
        }
        while (devotee_upgrade_cmd_able(this.engine.datamap) && (this.engine.datamap.prophetData.congregation.devoteeLevel < this.engine.datamap.prophetData.congregation.old.devoteeLevel)) {
            this.upgrade_devotee();
        }
    }

    recruit_preacher = () => {
        if (preacher_cmd_able(this.engine.datamap) === false) return;
        this.engine.datamap.prophetData.believers--;
        this.engine.datamap.prophetData.holiness -= preacher_cost(this.engine.datamap)
        this.engine.datamap.prophetData.congregation.preachers++;
    }

    upgrade_preacher = () => {
        if (preacher_upgrade_cmd_able(this.engine.datamap) === false) return;
        this.engine.datamap.prophetData.holiness -= preacher_upgrade_cost(this.engine.datamap)
        this.engine.datamap.prophetData.congregation.preacherLevel++;
    }

    recruit_devotee = () => {
        if (devotee_cmd_able(this.engine.datamap) === false) return;
        this.engine.datamap.prophetData.believers--;
        this.engine.datamap.prophetData.holiness -= devotee_cost(this.engine.datamap)
        this.engine.datamap.prophetData.congregation.devotee++;
    }

    upgrade_devotee = () => {
        if (devotee_upgrade_cmd_able(this.engine.datamap) === false) return;
        this.engine.datamap.prophetData.holiness -= devotee_upgrade_cost(this.engine.datamap)
        this.engine.datamap.prophetData.congregation.devoteeLevel++;
    }

    recruit_doorknocker = () => {
        if (doorknocker_cmd_able(this.engine.datamap) === false) return;
        this.engine.datamap.prophetData.believers--;
        this.engine.datamap.prophetData.holiness -= doorknocker_cost(this.engine.datamap)
        this.engine.datamap.prophetData.congregation.doorknocker++;
    }

    upgrade_doorknocker = () => {
        if (doorknocker_upgrade_cmd_able(this.engine.datamap) === false) return;
        this.engine.datamap.prophetData.holiness -= doorknocker_upgrade_cost(this.engine.datamap)
        this.engine.datamap.prophetData.congregation.doorknockerLevel++;
    }

    recruitParitioner(arg0: string) {
        console.log('recruiting!');

        let index = parseInt(arg0);
        let amount = 1;
        let cost = paritioner_cost(this.engine.datamap, index);

        if (this.engine.datamap.prophetData.devotion >= cost) {
            this.engine.datamap.prophetData.devotion -= cost;
            this.engine.datamap.prophetData.congregation.paritioners[index] += amount;
        }
    }

    recruitMaxParitioner(arg0: string) {
        let index = parseInt(arg0);
        let amount = paritioner_maxBuyAmount(index, this.engine.datamap);
        let cost = paritioner_buyNCost(index, this.engine.datamap, amount);

        if (this.engine.datamap.prophetData.devotion >= cost) {
            this.engine.datamap.prophetData.devotion -= cost;
            this.engine.datamap.prophetData.congregation.paritioners[index] += amount;
        }
    }

    recruitHalfMaxParitioner(arg0: string) {
        let index = parseInt(arg0);
        let amount = paritioner_buyHalfMax_amount(index, this.engine.datamap);
        let cost = paritioner_buyNCost(index, this.engine.datamap, amount);

        if (this.engine.datamap.prophetData.devotion >= cost) {
            this.engine.datamap.prophetData.devotion -= cost;
            this.engine.datamap.prophetData.congregation.paritioners[index] += amount;
        }
    }

    dayTriggerParitioners(days: number) {
        if (this.engine.datamap.prophetData.congregation.paritioners[0] > 0) this.proudDay(days)
        if (this.engine.datamap.prophetData.congregation.paritioners[1] > 0) this.wealthDay(days)
        if (this.engine.datamap.prophetData.congregation.paritioners[2] > 0) this.curiousDay(days)
        if (this.engine.datamap.prophetData.congregation.paritioners[3] > 0) this.smartDay(days)
        if (this.engine.datamap.prophetData.congregation.paritioners[4] > 0) this.dumbDay(days)
        if (this.engine.datamap.prophetData.congregation.paritioners[5] > 0) this.badDay(days)
    }

    dailyBelieverGain(days:number) {
        this.engine.datamap.prophetData.believers += this.data.DPUpgrades.dailyBelieverGain;
    }

    proudDay(days: number) {
        this.engine.datamap.prophetData.believers += proudBeliever_believer_gain(this.engine.datamap) * days
    }
    wealthDay(days: number) {
        this.engine.datamap.prophetData.congregation.USD += WealthyBeliever_USD_gain(this.engine.datamap) * days
        this.engine.datamap.prophetData.believers += WealthyBeliever_Believer_gain(this.engine.datamap) * days
    }
    curiousDay(days: number) {
        this.engine.datamap.prophetData.congregation.questions += this.engine.datamap.prophetData.congregation.paritioners[2] * days
    }
    smartDay(days: number) {
        this.engine.datamap.prophetData.congregation.crediblity += this.engine.datamap.prophetData.congregation.paritioners[3] * days
    }
    dumbDay(days: number) {
        this.engine.datamap.prophetData.congregation.crediblity -= this.engine.datamap.prophetData.congregation.paritioners[4] * days
    }
    badDay(days: number) {
        this.engine.datamap.prophetData.ex_believers = Math.max(0, this.engine.datamap.prophetData.ex_believers - this.engine.datamap.prophetData.congregation.paritioners[5] * days)
    }

    unlockAdornments() {
        let data = this.engine.datamap;
        if (UnlockAdornments_able(data) === false) return;
        else {
            data.prophetData.devotion -= UnlockAdornments_cost(data)
            data.followers.adornmentsUnlocked = true;
        }
    }
}

export interface FollowerData {
    faithfulCongregation: boolean;
    adornmentsUnlocked: boolean;
    devotedPrestige: number;
    DPUpgrades: DPUpgrades_Data;
}


export function followerDataInit() {
    return {
        faithfulCongregation: false,
        adornmentsUnlocked: false,
        devotedPrestige: 0,
        DPUpgrades: DPUpgrades_Init()
    }
}

