export function preacher_cost(data: Datamap) {
    let count = data.prophetData.congregation.preachers;
    return Math.pow(10, count) + 10;
}
function preacher_cmd_able(data: Datamap) {
    return (data.prophetData.believers >= 1) && (data.prophetData.holiness > preacher_cost(data))
}
export const recruit_preacher_cmd: DecoupledCommand = {
    name: 'Recruit',
    command: 'followers recruit preacher',
    able: preacher_cmd_able,
    hidden: () => { return false }
}



export function preacher_upgrade_cost(data: Datamap) {
    let count = data.prophetData.congregation.preacherLevel;
    return Math.pow(4, count) + 10;
}
function preacher_upgrade_cmd_able(data: Datamap) {
    return (data.prophetData.holiness > preacher_upgrade_cost(data))
}
export const upgrade_preacher_cmd: DecoupledCommand = {
    name: 'Upgrade',
    command: 'followers upgrade preacher',
    able: preacher_upgrade_cmd_able,
    hidden: () => { return false }
}


