//paritioners

export const ParitionerExpos: ExpoCurve[] = [
    { coefficient: 1.02, initial: 1000 },
    { coefficient: 1.04, initial: 1000000 },
    { coefficient: 1.06, initial: 1000000000 },
    { coefficient: 1.08, initial: 1000000000000 },
    { coefficient: 1.10, initial: 1000000000000000 },
    { coefficient: 1.12, initial: 1000000000000000000 },
]

export const Paritioners: ParitionerStats[] = [
    { name: 'Proud Believers', expo: ParitionerExpos[0] },
    { name: 'Wealthy Believers', expo: ParitionerExpos[1] },
    { name: 'Curious Believers', expo: ParitionerExpos[3] },
    { name: 'Educated Believers', expo: ParitionerExpos[2] },
    { name: 'Political Believers', expo: ParitionerExpos[4] },
    { name: 'Violent Believers', expo: ParitionerExpos[5] },
]

interface ParitionerStats {
    expo: ExpoCurve;
    name: string;
}

export function paritioner_cost(data: Datamap, index: number) {
    let owned = data.prophetData.congregation.paritioners[index];
    return expoI_getCost(Paritioners[index].expo, owned)
}

export function paritioner_able(data: Datamap, index: number) {
    let costable = paritioner_cost(data, index) <= data.prophetData.devotion
    let peopleable =  availablePeople(data) > 0;
    return costable && peopleable;
}
export function paritioner_maxBuyAmount(index: number, data: Datamap) {
    let costbound = expoI_maxBuyAmount(Paritioners[index].expo, data.prophetData.congregation.paritioners[index], data.prophetData.devotion)
    let peopleBound = availablePeople(data);
    return Math.min(costbound, peopleBound)
}

export function paritioner_maxBuyCost(index: number, data: Datamap) {
    let costbound = expoI_maxBuyCost(Paritioners[index].expo, data.prophetData.congregation.paritioners[index], data.prophetData.devotion)
    let peopleBound = expoI_buyNCost(Paritioners[index].expo, data.prophetData.congregation.paritioners[index], paritioner_maxBuyAmount(index, data))
    return Math.min(costbound, peopleBound)
}

export function paritioner_buyNCost(index: number, data: Datamap, n: number) {
    return expoI_buyNCost(Paritioners[index].expo, data.prophetData.congregation.paritioners[index], n)
}

export function paritioner_buyNextCost(index: number, data: Datamap, next: number) {
    return expoI_buyNextCost(Paritioners[index].expo, data.prophetData.congregation.paritioners[index], next)
}

export function paritioner_canBuyN(index: number, data: Datamap, n: number) {
    let cost = paritioner_buyNCost(index, data, n);
    return data.prophetData.devotion >= cost
}

export function paritioner_canBuyNext(index: number, data: Datamap, next: number) {
    let k = data.prophetData.congregation.paritioners[index];
    let n = next - k % next;
    return paritioner_canBuyN(index, data, n);
}

export function paritioner_buyHalfMax_amount(index: number, data: Datamap) {
    let full = paritioner_maxBuyAmount(index, data);
    let half = Math.floor(full / 2);
    return half;
}

export function paritioner_buyHalfMax_cost(index: number, data: Datamap) {
    let half = paritioner_buyHalfMax_amount(index, data);
    return paritioner_buyNCost(index, data, half)
}

export function proudBeliever_believer_gain(data: Datamap) {
    let gain =  data.prophetData.congregation.paritioners[0]
    if (data.prophetData.devotionUpgrades.def.a) gain = gain * 2
    gain += gain * data.prophetData.congregation.USDUpgrades.pamphlets
    gain += gain * data.followers.DPUpgrades.dailyBelieverGain;
    let harm = helper_prophet_exBeliever_harm(data);
    let realPossibleGain = Math.max(0, gain - harm);
    let realGain = Math.min(availablePeople(data),realPossibleGain);
    return realGain;
}

export function WealthyBeliever_USD_gain(data: Datamap) {
    let gain =  data.prophetData.congregation.paritioners[1];
    gain += gain * data.prophetData.congregation.USDUpgrades.tithe;
    gain += gain * .10 * data.pantheon.wealth.a

    return Math.floor(gain);
}

export function dailyBelieverMult (data :Datamap) {
    let mult = 1;
}

export function WealthyBeliever_Believer_gain(data: Datamap) {
    let gain =  data.prophetData.congregation.paritioners[1] / 100;
    gain = gain * data.pantheon.wealth.b;
    gain += gain * data.prophetData.congregation.USDUpgrades.tithe;
    gain += gain * data.followers.DPUpgrades.dailyBelieverGain;
    if (data.prophetData.devotionUpgrades.def.a) gain = gain * 2
    let harm = helper_prophet_exBeliever_harm(data);
    let realPossibleGain = Math.max(0, gain - harm);
    let realGain = Math.min(availablePeople(data),realPossibleGain);
    return Math.floor(realGain);
}

export function UnlockAdornments_cost(data: Datamap) {
    return 1e10
}
export function UnlockAdornments_able(data: Datamap) {
    return data.prophetData.devotion >= UnlockAdornments_cost(data)
}
export const UnlockAdornments: DecoupledCommand = {
    command: 'followers unlock adornments',
    name: "Unlock Adornments",
    hidden: (data) => { return data.prophetData.congregation.paritioners[1] === 0 || data.followers.adornmentsUnlocked },
    able: UnlockAdornments_able,
}

export function getDivinity(data: Datamap) {
    return data.followers.devotedPrestige
}

export function availableDivinity(data: Datamap) {
    return getDivinity(data) - spentDivinity(data);
}

export function spentDivinity (data: Datamap) {
    let base = 0;
    let dpu = DP_Upgrades;
    base += specificSpentDivinity(data, DP_Upgrades.DP_beliverGain);
    base += specificSpentDivinity(data, DP_Upgrades.DP_fasterRelicProduction);
    base += specificSpentDivinity(data, DP_Upgrades.DP_heavyHolyPower);
    base += specificSpentDivinity(data, DP_Upgrades.DP_monthMultiplier);
    base += specificSpentDivinity(data, DP_Upgrades.DP_superHolyPump);
    base += specificSpentDivinity(data, DP_Upgrades.DP_yearMultiplier);
    return base;
}

export function specificSpentDivinity (data: Datamap, dp : DivinityPurchase ) {
    return expoI_buyNCost(dp.expo, 0, dp.countFn(data))
}

export interface DivinityPurchase {
    command: DecoupledCommand;
    expo: ExpoCurve;
    countFn: (data: Datamap) => number;
    action: (data: Datamap) => void;
    description: string;
}


export function buy_divinityPurchase(data: Datamap, divinityPurcase: DivinityPurchase) {
    if (dp_able(data, divinityPurcase) === false) { return; }
    else {
        divinityPurcase.action(data)
    }
}

export function divinityPurcase_getCost(data: Datamap, divinityPurcase: DivinityPurchase) {
    return expoI_getCost(divinityPurcase.expo, divinityPurcase.countFn(data))

}

export function dp_able(data: Datamap, dp: DivinityPurchase) {
    return divinityPurcase_getCost(data, dp) <= availableDivinity(data);
}
export function dp_key(dp: DivinityPurchase) {
    return dp.command.command.split(' ')[2]
}

export function tierOne_Divinity_hidden(data: Datamap): boolean {
    return getDivinity(data) === 0
}



