import AppContext from "../../context/context";

import React from "react";
import CommandButtonFC from "./CommandButtonFC";
import CommandButton2 from "./CommandButton2";
import { currentSalary, availableExplorers, AreaData, getAreaGoal, getAreaETA, show_explore_area, AREA_STRS } from "../../engine/modules/Exploring";
import { AdornmentItem, getFullAdornmentItem } from "../../engine/modules/Adornments";
import TipFC from "./TipFC";

const AdornmentUI: React.FC = () => (
    <AppContext.Consumer>
        {({ data, submitCommand }) => (
            <div>
                <div className='HeaderText'>Adornments</div>
                <div>
                    Hire Believers to explore the world looking for Adornments for you. Not 100% useful yet!
                    <TipFC tip={`Only all the items from the ruins do something. Only 40% of the items from each other area have been given stats.`} />
                </div>
                <hr/>
                <div className='flexrow'>

                    <div className='flexcolumn flexequal'>
                        <div className='HeaderText'>Explorers</div>
                        <div>
                            No believers... no explorers.
                        </div>
                        <div className='HeaderText'>Info</div>
                        <div className='flexcolumn flexwrap'>
                            <div>
                                
                            Available Believers: {data.prophetData.believers}
                            </div><div>

                            Explorers: {data.adornments.explorers.explorerCount}
                            </div><div>

                            Available Explorers: {availableExplorers(data)}
                            </div>
                            <div>
                            Daily Salary: <span className='USDDiv'>${currentSalary(data)}</span>
                            </div>
                        </div>

                        <div className='HeaderText'>Commands</div>
                        <div className='flexrow flexwrap'>

                        <CommandButton2 command={() => 'adornments explorer hire'} name={() => `Hire Explorer`} />
                        <CommandButton2 command={() => 'adornments explorer fire'} name={() => `Fire Explorer`} />
                        </div>

                        <div className='HeaderText'>Party</div>
                        <div className='flexrow flexwrap'>
                        <div>
                        Explorers to send {data.adornments.explorers.readyForMission}
                        </div>
                        <CommandButton2 command={() => 'adornments explorer senddown'} name={() => `<-`} />
                        <CommandButton2 command={() => 'adornments explorer sendup'} name={() => `->`} />
                        </div>

                        <div className='HeaderText'>Areas</div>

                      
                        <div className='flexrow flexwrap'>

        {show_explore_area(data, AREA_STRS.RUINS_STRS.key) && <AreaBox commandKey='ruins' area={data.adornments.explorers.areas.ruins} />   }         
        {show_explore_area(data, AREA_STRS.MART_STRS.key) && <AreaBox commandKey='mart' area={data.adornments.explorers.areas.mart} />  }        
        {show_explore_area(data, AREA_STRS.SHOP_STRS.key) && <AreaBox commandKey='shop' area={data.adornments.explorers.areas.shop} />  }          
                        </div>

                    </div>
                    
                    <div className='flexcolumn flexequal'>
                        <div className='HeaderText'>Found Adornments</div>
                        Total Found: {data.adornments.adornmentCount}<br/>
                        Gems: {data.adornments.gems}<br/>
                        <div className='HeaderText'>Inventory</div>

                        <div className='flexrow flexwrap'>
                        {data.adornments.items.map(((itemData,index)=><ItemBox itemData={itemData} index={index} key={`item ${index}`} />))}
                        </div>
                        <div className='HeaderText'>Equipped</div>
                        <div className='flexrow flexwrap'>
                        {data.adornments.equiped.book !== null &&  <EquippedItemBox itemData={data.adornments.equiped.book} slot={'book'} />}
                        {data.adornments.equiped.cane !== null && <EquippedItemBox itemData={data.adornments.equiped.cane} slot={'cane'} /> }
                        {data.adornments.equiped.cap !== null && <EquippedItemBox itemData={data.adornments.equiped.cap} slot={'cap'} /> }
                        {data.adornments.equiped.ring !== null && <EquippedItemBox itemData={data.adornments.equiped.ring} slot={'ring'} />}
                        {data.adornments.equiped.robe !== null && <EquippedItemBox itemData={data.adornments.equiped.robe} slot={'robe'} /> }
                        </div>
                        { <CommandButton2 command={() => `adornments empty`} name={() => `Clear`} />}

                    </div>

                </div>


            </div>
        )}
    </AppContext.Consumer>
)

export default AdornmentUI;

const AreaBox: React.FC<{commandKey: string, area: AreaData}> = (props) => (
    <AppContext.Consumer>
        {({ data, submitCommand }) => (
            <div className='areabox'>
                            {props.commandKey}<br/>
                            Active: {props.area.beingExplored.toString()}<br/>
                            Current Explorers: {props.area.assignedExplorers}<br/>
                            Man-Days Spent: {props.area.investedDays}<br/>
                            Goal: {getAreaGoal(props.area, props.commandKey)}<br/>
                            ETA: {getAreaETA(props.area, props.commandKey)} days<br/>
                            Items Found: {props.area.exploredCount}
                            {props.area.beingExplored===false && <CommandButton2 command={() => `adornments explorer ${props.commandKey}`} name={() => `Send Party`} />}
                            {props.area.beingExplored===true && <CommandButton2 command={() => `adornments explorer stop${props.commandKey}`} name={() => `Recall Party`} />}
            </div>
        )}
    </AppContext.Consumer>
)

const ItemBox: React.FC<{itemData: AdornmentItem, index: number}> = (props) => (
    <AppContext.Consumer>
        {({ data, submitCommand }) => {
            let full = getFullAdornmentItem(props.itemData)
            return (
            <div className='itembox'>
                            {full.name}<br/>
                            {full.power}<br/>
                            Level: {full.data.level}
                            <div className='flexrow flexwrap'>
                            <CommandButton2 command={() => `adornments equip ${props.index}`} name={() => `Equip`} />
                            <CommandButton2 command={() => `adornments sell ${props.index}`} name={() => `Sell`} />
                            <CommandButton2 command={() => `adornments combine ${props.index}`} name={() => `Combine`} />
                            </div>
            </div>
        )}}
    </AppContext.Consumer>
)

const EquippedItemBox: React.FC<{itemData: AdornmentItem | null, slot: string}> = (props) => (
    <AppContext.Consumer>
        {({ data, submitCommand }) => {
            let full = getFullAdornmentItem({iNo:0,level:0,origin:""});
            if (props.itemData !== null) full = getFullAdornmentItem(props.itemData)
            return (
            <div className='itembox'>
                            {props.slot}<br/>
                            {full.name}<br/>
                            {full.power}<br/>
                            Level: {full.data.level}
                            <CommandButton2 command={() => `adornments unequip ${props.slot}`} name={() => `Unequip`} />
            </div>
        )}}
    </AppContext.Consumer>
)