import AppContext from "../context/context";
import React from "react";
import ProgressFC from "./comps/ProgressFC";
import GameTime from "../engine/modules/Gametime";

const BottomBar: React.FC<{}> = () => (
    <AppContext.Consumer>{({ data, submitCommand }) => (

        <div className='UIRow GameTimeRow'>
            <div style={{display:'flex', width:'100%'}}>
                <div className='timebar'>
            <ProgressFC color='green' background='white' value={data.gameTime.msprogress} max={GameTime.requiredMS} label={`Time - ${msToHM(data.gameTime.msprogress)}`} />
                </div>
                <div className='timebar'>
            <ProgressFC color='green' background='white' value={data.gameTime.day} max={GameTime.maxDays} label={`Day - ${data.gameTime.day}`} />
            </div>
            <div className='timebar'>
            <ProgressFC color='green' background='white' value={data.gameTime.month} max={GameTime.maxMonths} label={`Month - ${data.gameTime.month} ${MONTHS[data.gameTime.month - 1]}`} />
            </div>
            <div>
                {`Year - ${data.gameTime.year}`}
            </div>
            </div>
        </div>

    )}</AppContext.Consumer>
)

export default BottomBar;

function msToHM (msTime: number) {
    
    let hour = Math.floor(msTime/1000).toString();
    if (hour.length === 1) hour = '0' + hour;

    let minute = Math.floor((msTime%1000)/1000 * 60).toString();
    if (minute.length === 1) minute = '0' + minute;
    
    return `${hour}:${minute}`
}

const MONTHS = [
    'JAN',
    'FEB',
    'MAR',
    'APR',
    'MAY',
    'JUN',
    'JUL',
    'AUG',
    'SEP',
    'OCT',
    'NOV',
    'DEC',
]