import AppContext from "../../context/context";

import React from "react";

import CommandButtonFC from "./CommandButtonFC";

import { prophet_prestige_gain, next_holiness_mult, holiness_mult, reincation_mult, CMD_prophet_devotedPrestige, devotedPrestige_cost, Building_Multi } from "../../engine/modules/Prophet";

import { holiness_holyPumpUpgrade_command, holiness_holyPumpUpgradeCost, holiness_maxHolyPowerUpgrade_command, holiness_maxHolyPowerUpgradeCost, holiness_holypower1, holiness_holyPowerUpgradeCost as holiness_holyPowerEffectivenessUpgradeCost, holiness_buildingMulti_command, holiness_buildingMultiCost, holiness_believerRetention_command, holiness_believerRetentionCost, holiness_exRelations_command, holiness_exRelationsCost, holiness_devotionEffectiveness_command, holiness_devotionEffectivenessCost } from "../../engine/modules/PietyPurchases";
import ConfirmCommandButton from "./ConfirmCommandButton";
