export default ResetUI;




const DPUpgradeComp: React.FC<{ dp: DivinityPurchase }> = (props) => (
    <AppContext.Consumer>
        {({ data }) => (
            <CommandButtonFC {...props.dp.command}
                able={(datamap)=>{return dp_able(datamap, props.dp)}}
                tip={<div>
                    Cost: {divinityPurcase_getCost(data, props.dp)}<br />
                    {props.dp.description}<br />
                    Level : {props.dp.countFn(data)}
                </div>}
            />
        )}
    </AppContext.Consumer>
)

