import {Datamap, newDefaultMap} from './Datamap';
import * as _ from 'lodash'

export default abstract class CoreEngine {

    datamap: Datamap = newDefaultMap();
    loop: NodeJS.Timeout | undefined = undefined;

    constructor(public notify: VoidFunction){
        this.datamap.last = Date.now();
        this.start();
    }

    start = () => {
        if (!this.loop) {
            this.loop = setInterval(this.tick, 200);
        }
    }

    stop = () => {
        if (this.loop){
            clearTimeout(this.loop);
            this.loop = undefined;
        } 
    }

    save= () => {
        let datamap = JSON.stringify(this.datamap);
        localStorage.setItem('datamap', datamap);
    }

    load= () => {
        this.quietLoad();        
        this.notify();
    }

    reset = () => {
        this.datamap = newDefaultMap();
        this.notify();
    }

    quietLoad = () => {
        let combstr = localStorage.getItem('datamap');
        if (combstr != null){
            let oldobj = JSON.parse(combstr);
            this.datamap = newDefaultMap();
            _.merge(this.datamap, oldobj)
        }
    }

    tick = () => {
        
        let newnow = Date.now();
        let oldnow = this.datamap.last;
        let delta = newnow - oldnow;

        this.datamap.last = newnow;
        
        this.processDelta(delta)
        this.notify();
    }

    abstract processDelta (delta: number): void;
    abstract executeCommand (command: string): void;
 
}


