import AbstractModule from "./AbstractModule";
import * as TimePantheon from "./Pantheon/Time";
import ExpoCurve from "../interfaces/ExpoCurve";
import Exploring, { AreaData, AreaData_init, ExplorersData, ExplorersData_init } from "./Exploring";
import { getRandomInt } from "../util";
import { string } from "prop-types";
import { Datamap } from "../Datamap";

export default class Adornments extends AbstractModule {


    get data() {
        return this.engine.datamap.adornments;
    }

    handle_adornments_command = (cmdArray: string[]) => {
        switch (cmdArray[1]) {
            case 'explorer':
                this.exploring.handle_explore_command(cmdArray);
                break;

            case 'empty':
                this.data.items = [];
                break;

            case 'sell':
                let index_ = parseInt(cmdArray[2]);
                let item_ = this.data.items[index_]
                this.data.items.splice(index_, 1);
                this.data.gems += item_.level;
                break;

            case 'combine':
                this.combine(parseInt(cmdArray[2]));
                break;


            case 'equip':
                let index = parseInt(cmdArray[2]);
                let item = this.data.items[index]
                switch (item.iNo) {
                    case 1:
                        this.unequipCap()
                        this.data.equiped.cap = this.data.items.splice(index, 1)[0];
                        break;

                    case 2:
                        this.unequipCane()
                        this.data.equiped.cane = this.data.items.splice(index, 1)[0];
                        break;

                    case 3:
                        this.unequipRobe()
                        this.data.equiped.robe = this.data.items.splice(index, 1)[0];
                        break;

                    case 4:
                        this.unequipRing()
                        this.data.equiped.ring = this.data.items.splice(index, 1)[0];
                        break;

                    case 5:
                        this.unequipBook();
                        this.data.equiped.book = this.data.items.splice(index, 1)[0];

                        break;

                    default:
                        break;
                }
                break;

            case 'unequip':
                switch (cmdArray[2]) {
                    case 'cane':
                        this.unequipCane();
                        break;

                    case 'robe':
                        this.unequipRobe();

                        break;

                    case 'cap':
                        this.unequipCap();

                        break;

                    case 'book':
                        this.unequipBook();

                        break;

                    case 'ring':
                        this.unequipRing();
                        break;

                    default:
                        break;
                }
                break;

            default:
                break;
        }
    }

    unequipCap() {
        if (this.data.equiped.cap) {
            let toPush = this.data.equiped.cap;
            this.data.equiped.cap = null;
            if (toPush) this.data.items.push(toPush)
        }
    }

    combine = (index: number) => {
        let check_item = this.data.items[index];
        if (check_item.level >= 10) return;

        let matches: number[] = [];
        this.data.items.some((item, i_index) => {
            if (index === i_index) return false;
            if (check_item.iNo === item.iNo) {
                if (check_item.level === item.level) {
                    if (check_item.origin === item.origin) {
                        matches.push(i_index);
                        return true;
                    }
                }
            }
            return false;
        })
        if (matches.length > 0) {
            this.data.items[index].level++;
            this.data.items.splice(matches[0], 1)
        }

    }

    unequipCane() {
        if (this.data.equiped.cane) {
            let toPush = this.data.equiped.cane;
            this.data.equiped.cane = null;
            if (toPush) this.data.items.push(toPush)
        }
    }

    unequipRobe() {
        if (this.data.equiped.robe) {
            let toPush = this.data.equiped.robe;
            this.data.equiped.robe = null;
            if (toPush) this.data.items.push(toPush)
        }
    }

    unequipRing() {
        if (this.data.equiped.ring) {
            let toPush = this.data.equiped.ring;
            this.data.equiped.ring = null;
            if (toPush) this.data.items.push(toPush)
        }
    }

    unequipBook() {
        if (this.data.equiped.book) {
            let toPush = this.data.equiped.book;
            this.data.equiped.book = null;
            if (toPush) this.data.items.push(toPush)
        }
    }


    dayTrigger() {
        this.exploring.exploreDay();
    }

    getItem(origin: string, level: number) {
        this.data.adornmentCount++;
        let iNo = getRandomInt(1, 5)
        this.data.items.push({ origin, level, iNo })
    }

    exploring = new Exploring(this.engine)
}

const AdornmentCurve: ExpoCurve = {
    coefficient: 1e2,
    initial: 1e2
}

export interface AdornmentItem {
    origin: string,
    level: number,
    iNo: number,
}

export interface FullAdornmentItem {
    data: AdornmentItem;
    name: string;
    power: string;
}

export function getFullAdornmentItem(item: AdornmentItem): FullAdornmentItem {
    return {
        data: item,
        name: `${ItemNames[item.iNo]} of ${item.origin}`,
        power: PowerDictionary[ItemNames[item.iNo]](item)
    }
}

const ItemNames: string[] = [
    'unused',
    'Cap', 'Cane', 'Robe', 'Ring', 'Book'
]

const PowerDictionary: I_PowerDict = {
    unused: () => 'BROKE',
    Cap: capStats,
    Cane: caneStats,
    Robe: robeStats,
    Ring: ringStats,
    Book: bookStats,
}

function capStats(item: AdornmentItem) {
    switch (item.origin) {
        case 'ruins':
            return `+${item.level}x Piety Production`
            break;

        case 'mart':
            return `Nothing yet - make a suggestion!`
            return `+${item.level}x Devotion Production`
            break;

        case 'shop':
            return `Nothing yet - make a suggestion!`
            return `+${item.level}x USD Production`
            break;

        default:
            return 'ERROR CAP - NO ORIGIN'
            break;
    }
}

function caneStats(item: AdornmentItem) {
    switch (item.origin) {
        case 'ruins':
            return `Prayer gives +${item.level} Believers`
            break;

        case 'mart':
            return `Nothing yet - make a suggestion!`
            return `Prayer Gives +${item.level} Devotion`
            break;

        case 'shop':
            return `Nothing yet - make a suggestion!`
            return `Proseltyzing gives USD at ${item.level}0%  instead of Believers `
            break;

        default:
            return 'ERROR CANE - NO ORIGIN'
            break;
    }
}

