interface I_PowerDict {
    [key: string]: (item: AdornmentItem) => string;
}


export interface AdornmentData {
    adornmentCount: number;
    items: AdornmentItem[];
    equiped: {
        cap: AdornmentItem | null;
        cane: AdornmentItem | null;
        robe: AdornmentItem | null;
        ring: AdornmentItem | null;
        book: AdornmentItem | null;
    }
    gems: number;
    explorers: ExplorersData
}

export function adornmentDataInit(): AdornmentData {
    return {
        equiped: {
            book: null,
            cane: null,
            cap: null,
            ring: null,
            robe: null,
        },
        items: [],
        gems: 0,
        adornmentCount: 0,
        explorers: ExplorersData_init()
    }
}

