import React from 'react';
import AppContext from '../../context/context';
import CommandButtonFC from './CommandButtonFC';
import { Paritioners, paritioner_able, paritioner_maxBuyAmount, paritioner_cost, paritioner_maxBuyCost, paritioner_buyHalfMax_amount, paritioner_buyHalfMax_cost, proudBeliever_believer_gain, WealthyBeliever_USD_gain, WealthyBeliever_Believer_gain } from '../../engine/modules/Followers';
import { formatNumber } from '../../engine/util';


const ParitionerBox: React.FC<{ paritionerIndex: number }> = (props) => (
  <AppContext.Consumer>
    {({ data, submitCommand }) => (
      <div hidden={props.paritionerIndex > 0 && data.prophetData.congregation.paritioners[props.paritionerIndex - 1] === 0} className='ParitionerBox'>
        <div style={{ display: 'flex', justifyContent: 'space-between', flexDirection: 'column' }}>
          <div>
            <span>
           {Paritioners[props.paritionerIndex].name}
            </span>
            <div>
            Count: {data.prophetData.congregation.paritioners[props.paritionerIndex]}
          </div>
          </div>
          <div className='flexrow spacebetween'>

          <CommandButtonFC
            name={`Recruit`}
            command={`followers recruitparitioner ${props.paritionerIndex}`}
            able={(datamap) => { return paritioner_able(datamap, props.paritionerIndex) }}
            />
            <CommandButtonFC
              name={`Recruit 50% (${paritioner_buyHalfMax_amount(props.paritionerIndex, data)})`}
              command={`followers recruithalfmaxparitioner ${props.paritionerIndex}`}
              able={(datamap) => { return paritioner_buyHalfMax_amount(props.paritionerIndex, datamap) > 0 }}
              tip={(<div>
                Cost: {paritioner_buyHalfMax_cost(props.paritionerIndex, data).toExponential(3)}
              </div>)}
            />
          <CommandButtonFC
            name={`Recruit max (${paritioner_maxBuyAmount(props.paritionerIndex, data)})`}
            command={`followers recruitmaxparitioner ${props.paritionerIndex}`}
            able={(datamap) => { return paritioner_maxBuyAmount(props.paritionerIndex, datamap) > 0 }}
            tip={(<div>
              Cost: {paritioner_maxBuyCost(props.paritionerIndex, data).toExponential(3)}
            </div>)}
          />
              </div>

              <div className='flexrow spacebetween'>
                

          
          <div >
            Cost: {formatNumber(paritioner_cost(data, props.paritionerIndex))} Devotion
          </div>
              </div>

              <div>
                <ParitionerDetailts index={props.paritionerIndex} />
              </div>

          
        </div>
      </div>
    )}
  </AppContext.Consumer>
);

export default ParitionerBox;

export const ParitionerDetailts: React.FC<{ index: number }> = (props) => (
  <AppContext.Consumer>
    {({ data, submitCommand }) => (
      <div>
        {(props.index === 0) && `Bringing in ${proudBeliever_believer_gain(data)} other believers a day`}
        {(props.index === 1) && `Tithing ${WealthyBeliever_USD_gain(data)} USD a day`}
        {(props.index === 2) && `Asking ${data.prophetData.congregation.paritioners[props.index]} questions a day`}
        {(props.index === 3) && `Providing ${data.prophetData.congregation.paritioners[props.index]} credibility a day `}
        {(props.index === 4) && `Taking ${data.prophetData.congregation.paritioners[props.index]} crediblity a day`}
        {(props.index === 5) && `Fix ${data.prophetData.congregation.paritioners[props.index]} a day`}

        <br/>

        {(props.index === 1 && data.pantheon.wealth.b > 0) && `Recruiting ${WealthyBeliever_Believer_gain(data)} believers a day`}

      </div>
    )}
  </AppContext.Consumer>
)