import { Datamap } from "../Datamap";
import { DecoupledCommand, DecoupledCommand2 } from "../interfaces/ICommand";

export default class PietyPurchases {

}


export const congregationCost: number = 1e10;
export const reincarnationCost: number = 1e5;
export const holyPowerCost: number = 1e4;

function piety_purchase_canBuy (data: Datamap, cost: number) {
    return data.prophetData.piety >= cost;
}

export function piety_purchase_canBuy_congregation (data:Datamap) {
    return piety_purchase_canBuy(data, congregationCost)
}

export function piety_purchase_canBuy_reincarnation (data:Datamap) {
    return piety_purchase_canBuy(data, reincarnationCost)
}

export function piety_purchase_canBuy_holypower (data:Datamap) {
    return piety_purchase_canBuy(data, holyPowerCost)
}

export function piety_purchase_buy_congregation (data: Datamap) {
    if (piety_purchase_hide_congregation(data)) return
    if (!piety_purchase_canBuy_congregation(data)) return;
    data.prophetData.piety -= congregationCost;
    data.prophetData.congregation.unlocked = true;
}

export function piety_purchase_buy_reincarnation (data: Datamap) {
    if (piety_purchase_hide_reincarnation(data)) return
    if (!piety_purchase_canBuy_reincarnation(data)) return;
    data.prophetData.piety -= reincarnationCost;
    data.prophetData.prestigeUnlocked = true;
}

export function piety_purchase_buy_holypower (data: Datamap) {
    if (piety_purchase_hide_holypower(data)) return
    if (!piety_purchase_canBuy_holypower(data)) return;
    data.prophetData.piety -= holyPowerCost;
    data.prophetData.holyPowerUnlocked = true;
}

export function piety_purchase_hide_reincarnation (data: Datamap) {
    let hidden=  data.prophetData.relics[2] === 0 || data.prophetData.prestigeUnlocked
    return hidden;
}

export function piety_purchase_hide_congregation (data: Datamap) {
    let  hidden = data.prophetData.relics[4] === 0 || data.prophetData.congregation.unlocked
    return hidden;
}

export function piety_purchase_hide_holypower (data: Datamap) {
    let hidden=  data.prophetData.relics[1] === 0 || data.prophetData.holyPowerUnlocked
    return hidden;
}


export function holiness_holyPowerUpgradeCost (data: Datamap) {
    return Math.pow(10,data.holinessUpgrades.holypowerUpgrade + 1)
}
export const holiness_holypower1: DecoupledCommand = {
    name: "Holy Power Effectiveness",
    command: "prophet holypowerUpgrade",
    able: (data)=>{return holiness_holyPowerUpgradeCost(data) <= data.prophetData.holiness},
    hidden: (data)=>{return data.holinessUpgrades.maxHolyPowerUpgrade === 0}

}
export function holiness_holyPowerUpgrade_do (data: Datamap) {
    if (holiness_holypower1.able) if (!holiness_holypower1.able(data)) return;
    let cost = holiness_holyPowerUpgradeCost(data);
    data.holinessUpgrades.holypowerUpgrade ++;
    data.prophetData.holiness -= cost;
}

export function holiness_holyPumpUpgradeCost (data: Datamap) {
    return Math.pow(2, data.holinessUpgrades.holyPumpUpgrade + 1)
}
