export interface HolinessUpgrades {
    holypowerUpgrade: number;
    holyPumpUpgrade: number;
    maxHolyPowerUpgrade: number;
    believerRetention: number;
    exRelations: number;
    buildingMulti: number;
    devotionEffectiveness: number;
}

export function holinessUpgradesInit () {
    return {
        holypowerUpgrade: 0,
        holyPumpUpgrade: 0,
        maxHolyPowerUpgrade: 0,
        believerRetention: 0,
        exRelations: 0,
        buildingMulti: 0,
        devotionEffectiveness: 0,
    }
}

const MIRaCLES_COST = 1e30
export const UnlockMiracles: DecoupledCommand2 = {
    command:()=>'prophet unlockmiracles',
    name:()=> "Unlock Miracles",
    cost:()=> MIRaCLES_COST,
    hidden:(data)=>data.prophetData.relics[9] === 0 || data.rocketData.unlocked,
    able:(data)=>data.prophetData.piety >= MIRaCLES_COST
}