import AbstractModule from "./AbstractModule";
import Engine from "../Engine";

export default class NameAndCode extends AbstractModule {
    constructor(public engine: Engine) {
        super(engine);
    }

    nacCommand (commands: string []) {
        console.log(commands);
        
        switch (commands[1]) {
            case 'setname':
                this.data.name = commands[2];
                this.data.timeSet = Date.now();
                if (this.data.name === 'DREP'){
                    this.engine.prophet.devotedprestige();
                }
                break;

            case 'clearname':
                this.data.name = '';
                this.data.timeSet = 0;
        
            default:
                break;
        }
    }

    get data() {
        return this.engine.datamap.nacData;
    }

    
}

export interface NameAndCode_Data {
    name: string;
    timeSet: number;
}

export function NameAndCode_Init (): NameAndCode_Data {
    return {
        name: '', timeSet: 0
    }
}