import React from 'react';
import AppContext from '../../context/context';
import { DecoupledCommand2 } from '../../engine/interfaces/ICommand';
import TipFC from './TipFC';
import './CommandButtonFC.scss'
import { percentOf, formatNumber } from '../../engine/util';
   
const CommandButton2: React.FC<DecoupledCommand2> = (props) => (
    <AppContext.Consumer>
      {({ data, submitCommand }) => {
        let command = props.command(data);
        let name = props.name(data);
        let change = props.change?props.change(data):undefined;
        let cost;
        if (props.cost) {
          cost = props.cost(data)
        }

        return (
          <div hidden={props.hidden?props.hidden(data):false}>

          <button className='DCommandFC' disabled={props.able?!props.able(data):false}  onClick={()=>{submitCommand(command)}}>
            <div className='innerCommandRow'>
              <div className='flexcolumn'>
            <span>{name}</span>
            <span>{change}</span> 
      {cost&&<span>{formatNumber (cost)} <span>{props.costName}</span> {props.resource&&<span>({percentOf(cost, props.resource(data))})</span>}</span>}
            </div>
            <img hidden={props.img === undefined} src={props.img} height={'30px'} alt="" />
            </div>
            <TipFC tip={props.tip} />
        </button>
          </div>
      )}}
    </AppContext.Consumer>
  );

export default CommandButton2;
