import AppContext from "../../context/context";
import React from "react";
   
const StatFC: React.FC<{name: string, value: number, units?: string, estimate? :number}> = (props) => (
    <AppContext.Consumer>
      {({ data, submitCommand }) => {


        return (
            <div className='flexrow spacebetween'>
            <span>{props.name}</span>
            <div className='flexcolumn flexgrow'>

            <span className='flexrow flexend'>
                <span>
                    {`${props.value.toFixed(2)}`}
                </span>
                <span style={{textAlign:'right'}}>
                    {`${props.units}`}
                    </span>
            </span>
            {props.estimate !== undefined && 
            <span className='flexrow flexend'>
                <span>
                    {`${props.estimate.toFixed(2)}`}
                </span>
                <span style={{textAlign:'right'}}>
                    {`${props.units}`}
                </span>
            </span>}
            </div>
            </div>
      )}}
    </AppContext.Consumer>
  );

export default StatFC;