import ExpoCurve, { expoI_getCost, expoI_maxBuyAmount, expoI_buyNCost } from "../../interfaces/ExpoCurve";

import { DecoupledCommand2 } from "../../interfaces/ICommand";

import * as TIME from './Time';
import * as PRODUCTION from './Production';
import * as WEALTH from './Wealth';

import * as PERSUASION from './Persuasion';
import * as MAINTAINANCE from './Maintainance';
import * as GREED from './Greed';

import { Datamap } from "../../Datamap";
import { PANTHEON_COST } from "./Pantheons";


export interface BasePanteonUpgradeSet {
    key: string;
    do: (data:Datamap, command: string) => void;
}

export interface BasePantheonUpgrade {
    expo: ExpoCurve,
    command: DecoupledCommand2,
    countFn: (data: Datamap) => number;
    action: (data: Datamap, count: number) => void;
    description: string;
}

export function doPantheonUpgrade(data: Datamap, commands: string[]) {

    switch (commands[1]) {
        case 'unlock':
            data.prophetData.congregation.USD -= PANTHEON_COST;
            data.pantheon.unlocked = true;
            break;

        case TIME.ThisPantheon.key:
            TIME.ThisPantheon.do(data, commands[2])
            break;

        case PRODUCTION.ThisPantheon.key:
            PRODUCTION.ThisPantheon.do(data, commands[2])
            break;

        case WEALTH.ThisPantheon.key:
            WEALTH.ThisPantheon.do(data, commands[2]);
            break;

        default:
            break;
    }
}

export interface PantheonData {
    unlocked: boolean;
    
    time: TIME.UpgradeData;
    production: PRODUCTION.UpgradeData;
    wealth: WEALTH.UpgradeData;

    persuasion: PERSUASION.UpgradeData;
    maintainance: MAINTAINANCE.UpgradeData;
    greed: GREED.UpgradeData;
}

export function PantheonData_init(): PantheonData {
    return {
        unlocked: false,
        time: TIME.UpgradeData_init(), 
        production: PRODUCTION.UpgradeData_init(),
        wealth : WEALTH.UpgradeData_init(),

        persuasion : PERSUASION.UpgradeData_init(),
        maintainance : MAINTAINANCE.UpgradeData_init(),
        greed : GREED.UpgradeData_init(),
    }
}



export function buy_pantheonPurchase(data: Datamap, pantheonPurchase: BasePantheonUpgrade) {
    if (pantheonPurchase_able(data, pantheonPurchase) === false) { return; }
    else {
        data.prophetData.piety -= pantheonPurchaseCost(data, pantheonPurchase)
        pantheonPurchase.action(data, 1)
    }
}

export function pantheonPurchaseCost(data: Datamap, divinityPurcase: BasePantheonUpgrade) {
    return expoI_getCost(divinityPurcase.expo, divinityPurcase.countFn(data))

}

export function pantheonPurchase_able(data: Datamap, dp: BasePantheonUpgrade) {
    return pantheonPurchaseCost(data, dp) <= data.prophetData.piety;
}

export function pantheonPurchase_maxAmmount(data: Datamap, dp: BasePantheonUpgrade) {
    return expoI_maxBuyAmount(dp.expo, dp.countFn(data),data.prophetData.piety);
}
export function pantheonPurchase_maxCost(data: Datamap, dp: BasePantheonUpgrade) {
    let ammount = pantheonPurchase_maxAmmount(data, dp);
    return expoI_buyNCost(dp.expo, dp.countFn(data),ammount);
}

export function buyMax_pantheonPurchase(data: Datamap, dp: BasePantheonUpgrade) {
    let ammount = pantheonPurchase_maxAmmount(data,dp);
    let cost = pantheonPurchase_maxCost(data,dp)

        if (data.prophetData.piety >= cost) {
            data.prophetData.piety -= cost;
            dp.action(data, ammount);
        }
}