import { I_PropheData, init_ProphetData } from "./modules/Prophet";
import { GameTimeData, GameTimeInit } from "./modules/Gametime";
import { NavAreaData, NavAreaDataInit } from "./modules/NavArea";
import { HolinessUpgrades, holinessUpgradesInit } from "./modules/PietyPurchases";
import { FollowerData, followerDataInit } from "./modules/Followers";
import { AdornmentData, adornmentDataInit } from "./modules/Adornments";
import { RocketData, RocketDataInit } from "./modules/RocketTypes";
import { NameAndCode_Data, NameAndCode_Init } from "./modules/NameAndCode";
import { PantheonData, PantheonData_init } from "./modules/Pantheon/Types";

export interface Datamap {
    offline: {
        happening: boolean;
        totaltime: number;
    }
    x: number;
    y: number;
    z: number;
    ticks: number;
    last: number;
    ui: {
        innerWidth: number;
        innerHeight: number;
    }
    prophetData: I_PropheData;
    gameTime: GameTimeData;
    nav: NavAreaData,
    holinessUpgrades: HolinessUpgrades,
    followers: FollowerData,
    adornments: AdornmentData,
    rocketData: RocketData,
    nacData: NameAndCode_Data,
    automation: {
        auto_buyToNext: boolean,
    },
    pantheon: PantheonData;
}

export function newDefaultMap () {
    let map: Datamap = {
        offline: {
            happening: false,
            totaltime: 0,
        },
        x: 0, 
        y: 0, 
        z: 0,
        ticks: 0,
        last: Date.now(),
        ui: {
            innerHeight: 600,
            innerWidth: 800,
        },
        prophetData: init_ProphetData(),
        gameTime: GameTimeInit(),
        nav: NavAreaDataInit(),
        holinessUpgrades: holinessUpgradesInit(),
        followers: followerDataInit(),
        adornments: adornmentDataInit(),
        rocketData: RocketDataInit(),
        nacData: NameAndCode_Init(),
        automation: {
            auto_buyToNext: false,
        },
        pantheon: PantheonData_init(),
    }
    return map;
}