import React from 'react';
import AppContext from '../../context/context';
import { DecoupledCommand2 } from '../../engine/interfaces/ICommand';
import TipFC from './TipFC';
import './CommandButtonFC.scss'
import { percentOf, formatNumber } from '../../engine/util';
import ExpoCommand, { expoCommand_Able, expoCommand_Cost, expoCommand_maxAmmount } from '../../engine/interfaces/ExpoCommand';
   
const ExpoButton: React.FC<ExpoCommand> = (props) => (
    <AppContext.Consumer>
      {({ data, submitCommand }) => {
        
        let command = props.command;
        let name = props.name;
        let level = props.countFns.get(data);

        let change = props.change?props.change(data):undefined;

        let able = expoCommand_Able(data, props)
        let cost = expoCommand_Cost(data, props)
        let max = expoCommand_maxAmmount(data, props)

        return (
          <div hidden={props.hidden?props.hidden(data):false}>

          <div className='DCommandFC'  >
            <div className='innerCommandRow flexrow'>
              <button className='DCommandFC flexcolumn' disabled={!able}  onClick={()=>{submitCommand(command)}} >
        <span>{name}</span> 
        <span>Level {level}</span>
            {change && <span>{change}</span>} 
            <span>{formatNumber (cost)} <span>{props.currencyName}</span> </span>
            </button>
            <button className='DCommandFC' disabled={!able}  onClick={()=>{submitCommand(command+'max')}}>
            Buy Max<br/> 
            ({max})
            </button>
            <img hidden={props.img === undefined} src={props.img} height={'30px'} alt="" />
            </div>
            <TipFC tip={props.tip} />
        </div>



          </div>
      )}}
    </AppContext.Consumer>
  );

export default ExpoButton;
