import React, { ReactNode } from 'react';
import AppContext from '../../context/context';
import './TipFC.scss'
import ReactDOM from 'react-dom';

const TipFC: React.FC<{ tip?: ReactNode }> = (props) => {
  
  return ( <AppContext.Consumer>
    {({ data, submitCommand }) => (
      <div hidden={props.tip === undefined} >
        <div className='TipFC'>
          <div>
            {props.tip}
          </div>
        </div>
      </div>
    )}
  </AppContext.Consumer>
)}

export class classTipFC extends React.Component<{ tip: ReactNode }, {top: string|number, bottom: string|number}>{

  constructor(props: Readonly<{ tip: React.ReactNode; }>) {
    super(props)
    this.state = {
      top: "",
      bottom: ""
    }
  }

  static contextType = AppContext;

  componentDidMount () {
    this.setTB();
  }
  

  componentDidUpdate() {
    this.setTB();
  }

  setTB= ()=>{

    let height = window.innerHeight;

    let top: number | string = ""
    let bottom: number | string = ""

    let myDom = ReactDOM.findDOMNode(this);

    if (myDom) {


      let parent = ((ReactDOM.findDOMNode(this) as Element).parentElement as Element)

      if (parent) {

        parent = parent as Element;
        let parentBounds = parent.getBoundingClientRect()

        /*
        if (parentBounds.right > width / 2) {
          right = width - parentBounds.right;
          left = ""
        } else {
          right = ""
          left = parentBounds.left
        }*/

        if (parentBounds.bottom > height / 2) {
          bottom = height - parentBounds.top;
          top = ""
        } else {
          bottom = "";
          top = parentBounds.bottom;
        }

        this.setState({top, bottom});
      }
    }

  }

  render() {
    let props = this.props;

    //let width = window.innerWidth;

    //let left: number | string = ""
    //let right: number | string = ""
    

    

    return (
      <div hidden={props.tip === undefined} >
        <div className='TipFC' style={{top:this.state.top, bottom:this.state.bottom}}>
          <div>
            {props.tip}
          </div>
        </div>
      </div>
    )
  }

}

export default TipFC;

