import React from 'react';
import AppContext from '../../context/context';
import './CommandButtonFC.scss'
import { msToFullHMS } from '../../engine/util';


export default class OfflineOverlay extends React.Component { 

  static contextType = AppContext;

  fakeBuy () {

  }

  render () {
    let data = this.context.data;

    return (

        <div className='WarningBox' hidden={data.offline.happening === false}>
            <div className='WarningOverlay'>
              <div className='WarningPopup'>
                Offline Progress<br/>
                HH:MM:SS<br/>
                {msToFullHMS(data.offline.totaltime)}<br/>
                Relic gain is calculated all at once, then days pass.
              </div>
            </div>
        </div>
    )
  }
}


