import { Datamap } from "../Datamap";
import { DecoupledCommand, DecoupledCommand2 } from "../interfaces/ICommand";
import ExpoCurve from "../interfaces/ExpoCurve";

export default class DevotionPurchases {

}

export function doDevotionUpgrade (data: Datamap, command: string) {
    switch (command) {
        case 'shorterdays':
            data.prophetData.devotion -= ABC_cost(data);
            data.prophetData.devotionUpgrades.abc.a = true;
            break;

        case 'productiveprayer':
                data.prophetData.devotion -= ABC_cost(data);
                data.prophetData.devotionUpgrades.abc.b = true;
            break;

        case 'relicpower':
                data.prophetData.devotion -= ABC_cost(data);
                data.prophetData.devotionUpgrades.abc.c = true;
            break;

        case 'd':
                data.prophetData.devotion -= DEF_cost(data);
                data.prophetData.devotionUpgrades.def.a = true;
            break;

        case 'e':
                data.prophetData.devotion -= DEF_cost(data);
                data.prophetData.devotionUpgrades.def.b = true;
            break;

        case 'f':
                data.prophetData.devotion -= DEF_cost(data);
                data.prophetData.devotionUpgrades.def.c = true;
            break;
    
        default:
            break;
    }
}

function ABC_cost (data: Datamap) {
    return 1e6
}

function ABC_able (data: Datamap) {
    return data.prophetData.devotion >= ABC_cost(data) && ABC_hidden(data) === false;
}

export function ABC_hidden (data: Datamap) {
    return data.prophetData.congregation.paritioners[0] < 1 || ABC_bought(data);
}

export function ABC_bought (data: Datamap) {
    return data.prophetData.devotionUpgrades.abc.a || data.prophetData.devotionUpgrades.abc.b || data.prophetData.devotionUpgrades.abc.c
}


export const Devotion_Upgrade_ShorterDays: DecoupledCommand2 = {
    command: (data)=>'followers devotionupgrade shorterdays',
    name: (data)=>`(A) God of Time`,
    hidden: ABC_hidden,
    cost: ABC_cost,
    able: ABC_able,
    costName: 'Devotion',
    change: (data)=>`Days are only 12 seconds`,
}

