function DEF_cost (data: Datamap) {
    return 1e8
}

function DEF_able (data: Datamap) {
    return data.prophetData.devotion >= DEF_cost(data) && DEF_hidden(data) === false;
}

export function DEF_hidden (data: Datamap) {
    return data.prophetData.congregation.paritioners[1] < 1 || DEF_bought(data);
}

function DEF_bought (data: Datamap) {
    return data.prophetData.devotionUpgrades.def.a || data.prophetData.devotionUpgrades.def.b || data.prophetData.devotionUpgrades.def.c
}


