export interface DevotionUpgradeData {
    abc: { a: boolean, b: boolean, c: boolean };
    def: { a: boolean, b: boolean, c: boolean };
    ghijk: { a: boolean, b: boolean, c: boolean, d: boolean, e: boolean };
}

export function DevotionUpgradeData_init (): DevotionUpgradeData {
    return {
        abc: { a: false, b: false, c: false, },
        def: { a: false, b: false, c: false, },
        ghijk: { a: false, b: false, c: false, d: false, e: false },
    }
}