export interface SimData {
    mass: number;
            current_v: number;
            current_h: number;
            active: boolean;
            elapsedTime: number;
            impactJouls: number
}

export function SimDataInit ():SimData {
    return {
        mass: 1,
        current_h: 0,
        current_v: 0,
        impactJouls: 0,
        active: false,
        elapsedTime: 0,
    }
}

export interface DerivedData {
    estimatedTime: number;
    estimatedVMax: number;
    estimatedJouls: number;
}

export function DerivedDataInit ():DerivedData {
    return {
        estimatedTime: 0,
        estimatedVMax: 0,
        estimatedJouls: 0,
    }
}

export interface RocketData {
    unlocked: boolean;
    ball: {
        height: number,
        mass: number,
        derived: DerivedData;
        sim: SimData
    }
    thrown_ball: {
        initial_velocity: number,
        derived: DerivedData;
        sim: SimData
    }
    bouncyball: {
        bounces_upgrades: number;
        loss_upgrades: number;
        derived: DerivedData;
        sim: SimData & {bounces: number;};
    }
    joules: number;
}

export function RocketDataInit(): RocketData {
    return {
        unlocked: false,
        ball: {
            height: 0,
            mass: 0,
            derived: DerivedDataInit(),
            sim: SimDataInit(),
        },
        thrown_ball: {
            initial_velocity: 0,
            derived: DerivedDataInit(),
            sim: SimDataInit()
        },
        bouncyball: {
            bounces_upgrades: 0,
            loss_upgrades: 0,
            derived: DerivedDataInit(),
            sim: {...SimDataInit() ,bounces: 0},

        },
        joules: 0
    }
}
