import * as React from 'react';
import { Datamap, newDefaultMap } from '../engine/Datamap';

type AppContextType = {
  data: Datamap;
  submitCommand: (str:string)=>void;
}

// Create a context wrapping some shared state
let AppContext = React.createContext<AppContextType>({
  data: newDefaultMap(),
  submitCommand: (str)=>{},
});

export default AppContext;