import React, { ReactNode } from 'react';
import TipFC from './TipFC';

export interface ProgressProps {
    value: number;
    max: number;
    color?: string;
    background?: string;
    label: string | ReactNode; 
    textColor?: string
    tip?: ReactNode;
}

   
const ProgressFC: React.FC<ProgressProps> = (props) => {
    let trueValue = Math.min(props.value, props.max)
    let widthpercent = (trueValue * 100 / (props.max)).toString() + "%";
    if (widthpercent === 'NaN%') widthpercent = '0%';
    const [off, setOff] = React.useState(false);
    if (off) widthpercent = '100%'

    return (
        <div className='ProgressFC' style={{backgroundSize:widthpercent}}>
            <div className='InnerProgress' style={{width:widthpercent}} onClick={()=>setOff(!off)}>
                <span style={{whiteSpace:'nowrap'}}>
                    {props.label} 
                </span>
            </div>
            <TipFC tip={<div>{`${props.value}/${props.max}`}{props.tip}</div>} />
        </div>
    )
}


export default ProgressFC;