import React, { ReactNode } from 'react';
import AppContext from '../../context/context';
import { DecoupledCommand } from '../../engine/interfaces/ICommand';
import TipFC from './TipFC';
import './CommandButtonFC.scss'
import CommandButtonFC from './CommandButtonFC';

export interface CCBProps {
  command: DecoupledCommand;
  warning: ReactNode
}

export default class ConfirmCommandButton extends React.Component<CCBProps, {open:boolean}>{ 

  constructor(props:CCBProps) {
    super(props)

    this.state = {
      open: false
    }

  }

  static contextType = AppContext;

  fakeBuy = (ev: React.MouseEvent) => {
    if (ev.shiftKey) {
      this.context.submitCommand(this.props.command.command)
    } else this.setState({open:!this.state.open})
    
  }

  render () {
    let data = this.context.data;

    return (<div>
        <div className='FakeButton' hidden={this.props.command.hidden?this.props.command.hidden(data):false}>
        <button className='DCommandFC' disabled={this.props.command.able?!this.props.command.able(data):false} onClick={this.fakeBuy}>
            <div className='innerCommandRow'>
              <span>{this.props.command.name}</span> 
            <img hidden={this.props.command.img === undefined} src={this.props.command.img} height={'30px'} alt="" />
            </div>
            <TipFC tip={this.props.command.tip} />
        </button>
        </div>
        <div className='WarningBox' hidden={!this.state.open}>
            <div className='WarningOverlay' onClick={this.fakeBuy}>
              <div className='WarningPopup'>
                {this.props.warning}
                <div className='flexrow spacebetween'>
                <CommandButtonFC {...this.props.command}/>
                <button className='PlainD' onClick={this.fakeBuy}>
                  cancel
                </button>
                </div>
              </div>
            </div>
        </div>
    </div>)
  }
}


