import React from 'react';
import AppContext from '../../context/context';
import { DecoupledCommand } from '../../engine/interfaces/ICommand';
import TipFC from './TipFC';
import './CommandButtonFC.scss'
   
const CommandButtonFC: React.FC<DecoupledCommand> = (props) => (
    <AppContext.Consumer>
      {({ data, submitCommand }) => (
          <button className='DCommandFC' disabled={props.able?!props.able(data):false} hidden={props.hidden?props.hidden(data):false} onClick={()=>{submitCommand(props.command)}}>
            <div className='innerCommandRow'>
            <span>{props.name}</span> 
            <img hidden={props.img === undefined} src={props.img} height={'30px'} alt="" />
            </div>
            <TipFC tip={props.tip} />
        </button>
      )}
    </AppContext.Consumer>
  );

export default CommandButtonFC;
