import AbstractModule from "./AbstractModule";
import { Datamap } from "../Datamap";

export default class NavArea extends AbstractModule {

    get data (): NavAreaData {
        return this.engine.datamap.nav;
    }

    changeMainArea = (stringedNumber: string) => {
        let number = parseInt(stringedNumber);
        this.data.mainArea = number;
        this.data.subArea = 0;
    }

    changeSubArea = (stringedNumber: string) => {
        let number = parseInt(stringedNumber);
        this.data.subArea = number;
    }

}

export type NavAreaData = {
    mainArea: number;
    subArea: number;
}

export function NavAreaDataInit (): NavAreaData {
    return {
        mainArea: 0,
        subArea: 0,
    }
}

export function areaUnlocked(areaIndex:number, data: Datamap) {
    switch (areaIndex) {
        case 0:
            return true
            break;

        case 1: 
            return data.prophetData.prestigeUnlocked === true || data.prophetData.save.reincarnationCount > 0
            break;
        default:
            return false;
    }
}

